
import api from '../../../api/Api.configure';
import { RequestResponse } from '../../../models/RequestResponse';
import { DetailsLastFillQuestionResult } from '../models/queries/DetailsLastFillQuestion';
import { ApiBase } from '../../../api/Api';
import { ListHistoricVehicleResult } from '../models/queries/ListHistoricVehicle';
import { DetailsFillQuestionResult } from '../models/queries/DetailsFillQuestion';

export const endPoint: string = 'api/loggbook';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async details(fillQuestionId: string): Promise<RequestResponse<DetailsFillQuestionResult>> {
        return await (await api.get(`${endPoint}/${fillQuestionId}`)).data;
    }

    async lastFill(IdentifierVehicle: string): Promise<RequestResponse<DetailsLastFillQuestionResult>> {
        return await (await api.get(`${endPoint}/last-fill?IdentifierVehicle=${IdentifierVehicle}`)).data;
    }

    async listHistoric(IdentifierVehicle: string): Promise<RequestResponse<ListHistoricVehicleResult[]>> {
        return await (await api.get(`${endPoint}/list-historic?IdentifierVehicle=${IdentifierVehicle}`)).data;
    }
}
export const LoogbookApi = new Api();