import React, { useEffect, useState } from 'react'
import { AcceptedReportApi } from '../../../api/AcceptedReportApi';
import { Col, message, Modal, Row} from 'antd'
import { useForm } from 'react-hook-form';
import { HttpStatusCode } from '../../../../../models/RequestResponse';
import { Input, TextArea, Switch } from '../../../../../components/form/fields/WithFormItem';
import { useHistory, useParams } from 'react-router-dom';
import Form from '../../../../../components/form/form/Form';
import FormatDateServices from '../../../../../services/Utils/FormatDate/FormatDateServices';
import { UpdatePoliceComplaint_schema } from './UpdatePoliceComplaint.types';
import UpdatePoliceComplaintSchema from './UpdatePoliceComplaintSchema';
import "./UpdatePoliceComplaint.less";
import { UpdatePoliceComplaintCommand } from '../../../models/commands/UpdatePoliceComplaintCommand';


const UpdatePoliceComplaint: React.FC = () => {

    const { positionReportId } = useParams<{ positionReportId: string }>()
    const { goBack } = useHistory()
    const [loading, setLoading] = useState(false);
    const form = useForm<UpdatePoliceComplaint_schema>({ resolver: UpdatePoliceComplaintSchema });
    
    useEffect(() => {

        getReportComplaint(positionReportId);

    }, []);

    async function onSubmit(value: UpdatePoliceComplaintCommand) {
        setLoading(true);

        try {
                await AcceptedReportApi.updatePoliceComplaint({...value, id: positionReportId } as UpdatePoliceComplaintCommand);
                message.success("Ocorrência atualizada com sucesso!");
                goBack();
            }
        catch (e) {
            message.error("Erro crítico.")
        }
        finally {
            setLoading(false);
        }
    }

    function getReportComplaint(id: string) {
        setLoading(true);

        AcceptedReportApi.getReportComplaint(id)
            .then(result => {
                if (result.status === HttpStatusCode.NotFound) {
                    message.info("Ocorrência inválida")
                    goBack();
                }
                form.reset(result.data);
            })
            .finally(() => {
                setLoading(false);
            }
        );
    }

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="900px"
            title={`Editar ocorrência`}
            onCancel={() => { goBack() }}
            onOk={form.handleSubmit(onSubmit, err => console.log(err))}
        >
            <Form provider={form} loading={loading} >
                <div className="update-police-complaint-body">
                    <Row>
                        <Col span={6}>
                            <Input label="Código da Ocorrência" name="code" readOnly />
                        </Col>

                        <Col span={6}>
                            <Input label="Criado em" name="registedAt" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>

                        <Col span={4}>
                            <Switch label="Anônimo?" name="isAnonymus" checkedChildren={"Sim"} unCheckedChildren={"Não"} />
                        </Col>

                        <Col span={8}>
                            <Input label="Denunciante" name="whistleblower" required={!form.getValues().isAnonymus}/>
                        </Col>
                    </Row>

                    <Row>
                        <Col span={24}>
                            <TextArea label="Detalhes" rows={6} name="description" required />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={16}>
                            <Input label="Cidade" name="city" value="city" />
                        </Col>
                        <Col span={8}>
                            <Input label="Bairro" name="district" value="district" />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={12}>
                            <Input label="Rua" name="street" value="street" />
                        </Col>
                        <Col span={6}>
                            <Input label="Número" name="number" value="number" />
                        </Col>
                        <Col span={6}>
                            <Input label="CEP" name="zipCode" value="zipCode" />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={12}>
                            <Input label="Complemento" name="complement" />
                        </Col>

                        <Col span={12}>
                            <Input label="Tipo de localidade" name="locationType" />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={12}>
                            <TextArea label="Ponto de referência" name="referencePoint" />
                        </Col>

                        <Col span={12}>
                            <TextArea label="Características do local" name="locationFeatures" />
                        </Col>
                    </Row>
                </div>

            </Form>
        </Modal>
    )
}

export default UpdatePoliceComplaint;