import * as Yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { CreateUserCommand } from "../../models/commands/CreateUser";

const CreateUserSchema: Yup.SchemaOf<CreateUserCommand> = Yup.object().shape({

    name: Yup.string().nullable()
        .required("Nome é obrigatório"),

    warName: Yup.string().nullable()
        .required("Nome de guerra é obrigatório"),

    userName: Yup.string().nullable()
        .matches(/^[A-Za-z]+(?:[.][A-Za-z]+)*$/,"Usuário do sistema deve corresponder [a-z .]")
        .required("Usuário do sistema é obrigatório"),

    badge: Yup.mixed()
        .required("Graduação é obrigatório"),

    phoneNumber: Yup.string().nullable().required("Celular é obrigatório")
        .min(15, "Número de celular inválido")
        .max(16, "Número de celular inválido"),

    rg: Yup.string().nullable().required("RG é obrigatório")
        .min(11, "RG: xx.xxx.xxx-x")
        .max(13, "RG não é válido"),

    cpf: Yup.string().nullable().required("CPF é obrigatório")
        .min(14, "CPF: xxx.xxx.xxx-xx")
        .max(15, "CPF não é válido"),

    email: Yup.string().nullable()
        .email("Dígite um email válido")
        .required("Email é obrigatório"),

    occupation: Yup.string().nullable().required("Função é obrigatório"),
})

export default yupResolver<CreateUserCommand>(CreateUserSchema);