import { RequestResponse } from '../../../models/RequestResponse';
import { ApiBase } from '../../../api/Api';
import api from '../../../api/Api.configure';
import { AllMarkersResult } from '../models/queries/AllMarkers';

export const endPoint: string = 'api/map-2d';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async getAllMarkers(): Promise<RequestResponse<AllMarkersResult>> {
        return (await api.get(`${endPoint}/get-all-markers`)).data
    }
}
export const Map2dApi = new Api();