import React, { useEffect, useMemo, useState } from 'react'
import { AcceptedReportApi } from '../../api/AcceptedReportApi';
import { AcceptedReportsSituationType, ListAcceptedReportsResult } from '../../models/queries/ListAcceptedReportsResult';
import { ColumnsType } from 'antd/lib/table';
import { NotificationHub } from '../../../../hub/Notifications/NotificationHub';
import { ReceiveUpdateAcceptPoliceReportMessage } from '../../../../hub/Notifications/models/ReceiveUpdateAcceptPoliceReportMessage';
import { PageHeader, Table } from 'antd';
import { useHistory } from 'react-router-dom';
import FormatDateServices from '../../../../services/Utils/FormatDate/FormatDateServices';
import MenuTablet from '../../../../components/table/MenuTable/MenuTable';
import SituationReport from './components/situation-report/SituationReport';

import "./ListAcceptedReports.less"
import { PositionReportType } from '../../models/common/CommonPoliceReport';

const ListAcceptedReports: React.FC = () => {

    const { push } = useHistory();
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState<ListAcceptedReportsResult[]>();

    const colunas = useMemo(() => [
        {
            title: 'Código da ocorrência',
            dataIndex: 'code',
            key: 'code',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Tipo',
            dataIndex: 'type',
            key: 'type',
            onCell: (data) => ({ data }),
            render: (value: PositionReportType) => value == PositionReportType.COMPLAINT ? "Ocorrência" : "Denúncia"
        },
        {
            title: 'Descrição',
            dataIndex: 'description',
            key: 'description',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Prefixo do veículo',
            dataIndex: 'prefix',
            key: 'prefix',
            onCell: (data) => ({ data }),
            render: (value: string) => value ?? "-"
        },
        {
            title: 'Situação Ocorrência',
            key: 'situation',
            dataIndex: "situation",
            onCell: (data) => ({ data }),
            render: (situation: AcceptedReportsSituationType) => <SituationReport situation={situation} />
        },
        {
            title: 'Iniciado em',
            dataIndex: 'acceptedAt',
            key: 'acceptedAt',
            onCell: (data) => ({ data }),
            render: (value?: Date) => value ? FormatDateServices.formatDateTime(value) : "-"
        },
        {
            title: 'Finalizado em',
            dataIndex: 'finishedAt',
            key: 'finishedAt',
            onCell: (data) => ({ data }),
            render: (value?: Date) => value ? FormatDateServices.formatDateTime(value) : "-"
        },
        {
            title: 'Ações',
            key: 'cancelate',
            onCell: (data) => ({ tabletOption: true, data }),
            render: (value, record) => <MenuTablet menu={[
                { name: "Editar", onClick: () => getRouteByReportType(record) },
                { name: "Cancelar", onClick: () => push(`/accepted-reports/cancel/${record.id}`) }
            ]} />
        },
    ] as ColumnsType<ListAcceptedReportsResult>, []);


    useEffect(() => {
        onListAcceptedReports();

        const key = NotificationHub.onReceiveUpdateAcceptPoliceReport(handlerNewFillQuestion);

        return () => {
            NotificationHub.unRegister(key);
        }
    }, []);

    function getRouteByReportType(record: ListAcceptedReportsResult) {
        if (record.type == PositionReportType.COMPLAINT) {
            return push(`/accepted-reports/edit-complaint/${record.id}`);
        }

        return push(`/accepted-reports/edit-delation/${record.id}`);
    }
    
    function handlerNewFillQuestion(message: ReceiveUpdateAcceptPoliceReportMessage) {
        console.log(message)
        setData(old => old?.map(e => {
            if (e.id === message.positionReportId) {
                let situation = AcceptedReportsSituationType.PENDENCY;


                if (message.acceptedAt) {
                    situation = AcceptedReportsSituationType.INITIATED;
                }

                if (message.finishedAt) {
                    situation = AcceptedReportsSituationType.FINISHED;
                }

                if (message.canceledAt) {
                    situation = AcceptedReportsSituationType.CANCELED;
                }
                return {
                    ...e,
                    id: e.id,
                    acceptedAt: message.acceptedAt,
                    canceledAt: message.canceledAt,
                    finishedAt: message.finishedAt,
                    situation: situation,
                    update: true
                }
            }
            return e
        }))
    }

    async function onListAcceptedReports() {
        setLoading(true)
        try {
            let result = await AcceptedReportApi.listAll();
            setData(result.data)
        }
        finally {
            setLoading(false)
        }
    }

    const renderBodyCell = (props: any) => {

        let onClick = () => {
            push(`accepted-reports/${props.data.id}`);
            setData(old => old?.map(e => {
                if (e.id === props.data.id) {
                    return { ...e, update: undefined }
                }
                return e
            }))
        }

        if (props.tabletOption) {
            onClick = () => { }
        }

        return <td {...props} onClick={onClick} />
    }

    const renderBodyRow = (props: any) => {

        if (props?.record?.update) {
            return <tr {...props} style={{ background: "#FAD24A" }} />;
        }

        return <tr {...props} />;
    };

    return (
        <>
            <PageHeader title="Relatórios de acorrências" />
            <Table rowKey="id"
                columns={colunas}
                dataSource={data} size="middle"
                components={{
                    body: {
                        row: renderBodyRow,
                        cell: renderBodyCell,
                    }
                }}
                loading={loading}
                pagination={false}
                onRow={(record) => {
                    return {
                        onDrag: () => { },
                        record: record
                    }
                }}
            />
        </>
    )
}

export default ListAcceptedReports
