export interface CreateUserCommand {
    name: string;
    warName: string;
    userName: string;
    badge: BadgesTypes;
    phoneNumber: string;
    rg: string;
    cpf: string;
    email: string;
    occupation: string;
}

export interface UpdateUserCommand {
    id: string;
    name: string;
    warName: string;
    userName: string;
    badge: BadgesTypes;
    phoneNumber: string;
    rg: string;
    cpf: string;
    email: string;
    occupation: string;
}

export interface CreateUserResult {
    succeeded: boolean;
    userName: string;
    password: string;
    errors: IdentityError[];
}

export interface IdentityError {
    code: string;
    description: string;
}

export enum BadgesTypes {
    SOLDIER,
    CABLE,
    THIRD_SERGEANT,
    SECOND_SERGEANT,
    FIRST_SERGEANT,
    SERGEANT,
    OFFICER_CANDIDATE,
    SECOND_LIEUTENANT,
    FIRST_LIEUTENANT,
    CAPTAIN,
    MAJOR,
    LIEUTENANT_COLONEL,
    COLON
}