
import { RequestResponse } from '../../../models/RequestResponse';
import { ApiBase } from '../../../api/Api';
import api from '../../../api/Api.configure';
import { ListVehiclesResult } from '../models/queries/ListVehicles';
import { CreateVehicleCommand, CreateVehicleResult } from '../models/commands/CreateVehicle';

export const endPoint: string = 'api/vehicles';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async listAll(): Promise<RequestResponse<ListVehiclesResult[]>> {
        return (await api.get(`${endPoint}/list-all`)).data
    }

    async Create(command: CreateVehicleCommand): Promise<RequestResponse<CreateVehicleResult>> {
        return (await api.post(`${endPoint}`, command)).data
    }
}
export const VehicleApi = new Api();