import { Input } from 'antd';
import React, { useState } from 'react'
import PlacesAutocomplete, { geocodeByAddress, getLatLng } from 'react-places-autocomplete';
import { SearchInputTypes } from './SearchInput.types';
import "./SearchInput.less"
const SearchInput: React.FC<SearchInputTypes> = ({ onSearch }) => {

    const [address, setAddress] = useState("")


    const handleChange = (address: string) => {
        setAddress(address);
    };

    const handleSelect = (address: string) => {
        console.log(address)
        geocodeByAddress(address)
            .then(results => getLatLng(results[0]))
            .then(latLng => onSearch(latLng.lat, latLng.lng))
            .catch(error => console.error('Error', error));
    };


    return (
        <PlacesAutocomplete
            value={address}
            onChange={handleChange}
            onSelect={handleSelect}

            searchOptions={{ bounds: new google.maps.LatLngBounds({ lat: -25.634098, lng: -54.647250 }, { lat: -24.701925, lng: -53.449265 }) }}
        >
            {({ getInputProps, getSuggestionItemProps, suggestions, loading }) => (
                <div className="autocomplete-root">
                    <Input.Search className="input-container" {...getInputProps()} />
                    <div className="autocomplete-dropdown-container">
                        {loading && <div>carregando...</div>}
                        {suggestions.map((suggestion: any) => (
                            <div {...getSuggestionItemProps(suggestion)} className="option" >
                                <span {...getSuggestionItemProps(suggestion)}  >{suggestion.description}</span>
                            </div>
                        ))}
                    </div>
                </div>
            )}
        </PlacesAutocomplete>
    )
}

export default SearchInput
