import { RequestResponse } from '../../../models/RequestResponse';
import { ApiBase } from '../../../api/Api';
import api from '../../../api/Api.configure';
import { GetReverseGeocodeQuery, GetReverseGeocodeResult } from '../models/queries/GetReverseGeocode';

export const endPoint: string = 'api/geo-map';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async ReverseGeocode(query : GetReverseGeocodeQuery): Promise<RequestResponse<GetReverseGeocodeResult>> {
        return (await api.get(`${endPoint}/reverse-geocode`, {params: query})).data
    }
}
export const GeoMapApi = new Api();