import { EventType } from "./event.types";

export class EventService {

    static callbacks: any = {};
    static LastUid = 0;

    /**
    * @param {string} eventName
    * @param {*} data
    */
    static trigger<T>(eventName: EventType, data: T | null = null) {
        if (this.callbacks[eventName]) {
            Object.keys(this.callbacks[eventName]).forEach((id) => {
                this.callbacks[eventName][id](data);
            });
        }
    }


    /**
    * @param {string} eventName name of event
    * @param {Function} callback
    */
    static listen<T>(eventName: EventType, callback: (data: T) => void) {

        if (!this.callbacks.hasOwnProperty(eventName)) {
            this.callbacks[eventName] = {};
        }


        let id = 'uid_' + String(++this.LastUid);
        this.callbacks[eventName][id] = callback;

        const unregister = () => {
            this.unlisten(eventName, id);
        }

        return unregister;
    }


    /**
    * @param {string} eventName name of event
    * @param {string} id callback identifier
    */
    static unlisten(eventName: EventType, id: string) {
        delete this.callbacks[eventName][id];
    }
}