import React, { useCallback, useEffect, useState } from 'react'
import { Select } from '../../../../components/form/fields/WithFormItem'
import { useForm } from 'react-hook-form'
import { useHistory, useLocation, useParams } from 'react-router-dom'
import Form from '../../../../components/form/form/Form'
import Modal from 'antd/lib/modal/Modal'
import { message, Select as SelectAntd } from 'antd'
import { ListDeviceResult } from '../../models/queries/ListDevice'

import "./SelectDevice.less"
import { DeviceApi } from '../../api/DeviceApi'
import { EventService } from '../../../../services/Utils/event/event.service'
import { EventType } from '../../../../services/Utils/event/event.types'
const Option = SelectAntd.Option;

const SelectDevice: React.FC = () => {
    const { vehicleId } = useParams<{ vehicleId: string }>()
    const { state } = useLocation<any>();
    const { goBack } = useHistory();

    const [loading, setLoading] = useState(false);
    const [devices, setDevices] = useState<ListDeviceResult[]>([])
    const form = useForm({
        defaultValues: {
            deviceId: state.deviceId
        }
    });

    useEffect(() => {
        setLoading(true)

        DeviceApi.listAll()
            .then(result => {
                setDevices(result.data!)
            })
            .finally(() => setLoading(false))

    }, []);

    const onSubmit = useCallback(async (value) => {
        setLoading(true)
        try {
            await DeviceApi.defineVehicle({ deviceId: value.deviceId, vehicleId });
            goBack();
            EventService.trigger(EventType.update_device,)
        }
        catch (e) {
            message.error(e);
        }
        finally {
            setLoading(false);
        }
    }, [])

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="40%"
            title="Configuração do veículo"
            okText="Salvar"
            onOk={form.handleSubmit(onSubmit)}
            onCancel={() => { goBack() }}
        >
            <div className="view-select-device-body">
                <Form provider={form} loading={loading}>
                    <Select label="Tablet" name="deviceId" >
                        {devices.map(e => <Option key={e.id} value={e.id} title={e.name} >{e.name}</Option>)}
                    </Select>
                </Form>
            </div>

        </Modal>
    )
}

export default SelectDevice
