import { Button, Dropdown, Menu} from 'antd';
import React from 'react'
import { MenuTabletProps } from './MenuTable.types';
import { DownOutlined } from '@ant-design/icons';

const MenuTablet: React.FC<MenuTabletProps> = (props) => {

  const menu = <Menu >
    {props.menu.map(e => <Menu.Item key={e.name} onClick={e.onClick}>
      {e.name}
    </Menu.Item>)}
  </Menu>

  return (

    <Dropdown overlay={menu}>
      <Button>
        Ações <DownOutlined />
      </Button>
    </Dropdown>
  )
}

export default MenuTablet
