export interface CreateVehicleCommand {
    licensePlate: string | null;
    prefix: string | null;
    brandId: string | null;
    model: string | null;
    color: string | null;
    year?: string | null;
    renavam: string | null;
    chassi: string | null;
    situation?: string | null;
    details?: string | null;
    description?: string | null;
    initialKm?: number | null;
    fuel?: FuelType | null;
}

export interface CreateVehicleResult {
    id: string;
}

export enum FuelType {
    Gasoline,
    Alcohol,
    Gasoline_Alcohol,
    Diesel
}