import React, { useEffect, useState } from 'react'
import { Button, Col, Row } from 'antd'
import { Marker, Popup } from 'react-leaflet'
import { iconPoliceComplaintReport } from '../../Icons/Icons'
import { NotificationHub } from '../../../../../../../hub/Notifications/NotificationHub'
import { PoliceComplaintReportMarkerProps } from './PoliceComplaintMarker.types'
import { ReceivePoliceComplaintReportMessage } from '../../../../../../../hub/Notifications/models/ReceivePoliceComplaintReportMessage'
import FormatDateServices from '../../../../../../../services/Utils/FormatDate/FormatDateServices'

import "./PoliceComplaintMarker.less";
import { Link } from 'react-router-dom'

const PoliceComplaintMarker: React.FC<PoliceComplaintReportMarkerProps> = (props) => {

    const [positions, setPositions] = useState<ReceivePoliceComplaintReportMessage[]>([])

    useEffect(() => {
        const keyLocation = NotificationHub.onReceivePoliceComplaintReport((message) => handlerReceivePoliceReport(message));

        return () => {
            NotificationHub.unRegister(keyLocation);
        }
    }, [])

    useEffect(() => {
        setPositions(props.policeReports);
    }, [props.policeReports])


    function handlerReceivePoliceReport(message: ReceivePoliceComplaintReportMessage) {
        setPositions(old => [...old, message]);
    }

    return (
        <>
            {positions?.map(position => (
                <Marker
                    icon={iconPoliceComplaintReport}
                    key={position.policeReportId}
                    position={{
                        lat: position.latitude,
                        lng: position.longitude
                    }}>
                    <Popup className="police-complaint-report-marker-popup">
                        <div className="police-complaint-report-marker-container">
                            <div className="police-complaint-report-marker-title">{position.code} - Ocorrência</div>
                            <div className="police-complaint-report-marker-body">
                                <Row>
                                    <Col span={12}>
                                        <span className="police-complaint-report-marker-label">Data: </span>
                                        {FormatDateServices.formatDate(position.registedAt)}
                                    </Col>

                                    <Col span={12}>
                                        <span className="police-complaint-report-marker-label">Hora: </span>
                                        {FormatDateServices.formatTime(position.registedAt)}
                                    </Col>

                                </Row>

                                <div>
                                    <span className="police-complaint-report-marker-label">Solicitante: </span>
                                    {position.whistleblower ?? "Anônimo"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Usuário: </span>
                                    {"Administrador"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Detalhes: </span>
                                    {position.description}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Cidade: </span>
                                    {position.city ?? "_"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Rua: </span>
                                    {position.street ?? "_"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Bairro: </span>
                                    {position.district ?? "_"}
                                </div>

                                <Row>
                                    <Col span={12}>
                                        <span className="police-complaint-report-marker-label">Número: </span>
                                        {position.number ?? "_"}
                                    </Col>
                                    <Col span={12}>
                                        <span className="police-complaint-report-marker-label">CEP: </span>
                                        {position.zipCode ?? "_"}
                                    </Col>
                                </Row>

                                <div>
                                    <span className="police-complaint-report-marker-label">Complemento: </span>
                                    {position.complement ?? "_"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Tipo de localidade: </span>
                                    {position.locationType ?? "_"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Ponto de referência: </span>
                                    {position.referencePoint ?? "_"}
                                </div>

                                <div>
                                    <span className="police-complaint-report-marker-label">Características do local: </span>
                                    {position.locationFeatures ?? "_"}
                                </div>

                                <Row style={{ justifyContent: "center" }}>
                                    <Link to={`map-2d/report/${position.policeReportId}`}>
                                        <Button >Mais detalhes da Ocorrência</Button>
                                    </Link>
                                </Row>
                            </div>
                        </div>
                    </Popup>
                </Marker>
            ))}
        </>
    )
}

export default PoliceComplaintMarker
