import React, { useContext, useEffect } from 'react'
import { useMap } from "react-leaflet";
import L from "leaflet";
import ReactDOM from 'react-dom';
import SearchInput from './components/SearchInput/SearchInput';
import { MapContext } from '../MapContextProvider/MapContextProvider';

const SearchAddress: React.FC = () => {

    const map = useMap();
    const {setSelectedPosition} = useContext(MapContext);


    useEffect(() => {
        const legend = new L.Control({ position: "topleft" });

        legend.onAdd = () => {
            const div = L.DomUtil.create("div", "legend");
            ReactDOM.render(<SearchInput onSearch={(lat, lng) => setSelectedPosition({lat,lng})} />, div)
            return div;
        };

        legend.addTo(map);

        return () => { legend.remove() };
    }, [map, setSelectedPosition]);


    return null;
}

export default SearchAddress
