import api from '../../../api/Api.configure';
import { ApiBase } from "../../../api/Api";
import { RequestResponse } from '../../../models/RequestResponse';
import { IListApproachResult } from '../models/queries/ListApproach';
import { DetailsApproachResult } from '../models/queries/DetailsApproach';

export const endPoint: string = 'api/loggbook';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async listAllApproach(): Promise<RequestResponse<IListApproachResult[]>> {
        return (await api.get(`${endPoint}/list-all-approach`)).data;
    }

    async getApproach(approachId: string): Promise<RequestResponse<DetailsApproachResult>> {
        return (await api.get(`${endPoint}/approach/${approachId}`)).data;
    }

}

export const LogbookApi = new Api();