import { Modal, Space, Spin } from 'antd';
import React, { createRef, useEffect, useRef, useState } from 'react'
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { ICancelateReportParams, ICancelateReportProps, IModalFunc } from './CancelateReport.types';
import { AcceptedReportApi } from '../../api/AcceptedReportApi';
import { useParams, useHistory } from 'react-router-dom';
import { DetailPoliceReportResult } from '../../models/queries/DetailsAcceptedReports';

const { confirm } = Modal;

const CancelateReport: React.FC<ICancelateReportProps> = () => {

    const { positionReportId } = useParams<ICancelateReportParams>();
    const { goBack } = useHistory();

    const [model, setModel] = useState<DetailPoliceReportResult>()
    const [refModal, setRefModal] = useState<IModalFunc>()
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        showConfirm();

        setLoading(true);
        AcceptedReportApi.getItem(positionReportId)
            .then(data => setModel(data.data))
            .finally(() => setLoading(false))
    }, [])

    useEffect(() => {
        if (loading === false) {
            refModal?.update({
                content: renderContent()
            })
        }
    }, [loading, refModal])

    function renderContent() {
        return (
            <div>
                {loading && <Space size="middle">
                    <Spin />
                </Space>}

                {!loading && <div>
                    <h3>Confirme o cancelamento</h3>
                    <div>Código: { model?.code}</div>
                    <div>Descição: { model?.description}</div>
                </div>}
            </div>
        )
    }

    function showConfirm() {

        let modal = confirm({
            title: 'Cancelar ocorrência',
            icon: <ExclamationCircleOutlined />,
            content: renderContent(),
            okText: 'Cancelar',
            okType: 'danger',
            cancelText: 'Voltar',
            onOk() {
                AcceptedReportApi.cancelReport(positionReportId);
            },
            onCancel() {
                goBack()
            },
        })

        setRefModal(modal)
    }

    return null;
}

export default CancelateReport
