import { notification } from "antd";
import { ReceiveCancelSOSVehicleMessage } from "../../hub/Notifications/models/ReceiveCancelSOSVehicleMessage";
import { ReceiveNeedSOSVehicleMessage } from "../../hub/Notifications/models/ReceiveNeedSOSVehicleMessage";
import { NotificationHub } from "../../hub/Notifications/NotificationHub";
import { globalStore } from "../../stores/global-store";
import { setOperationMode } from "../../stores/System/System.store";
import { SystemOperationModeType } from "../../stores/System/System.types";

export class SynchronizeService {
    tokenNeedSOS: string = "";
    tokenCancelSOS: string = "";


    init() {
        this.tokenNeedSOS = NotificationHub.onReceiveNeedSOS(this.onReceiveNeedSOS);
        this.tokenCancelSOS = NotificationHub.onReceiveCancelSOS(this.onReceiveCancelSOS);
    }

    dispose() {
        NotificationHub.unRegister(this.tokenNeedSOS);
        NotificationHub.unRegister(this.tokenCancelSOS);
    }

    onReceiveNeedSOS(message: ReceiveNeedSOSVehicleMessage) {
        globalStore.dispatch(setOperationMode(SystemOperationModeType.IN_SOS))
        
        notification.error({
            duration: 100,
            className: "need-sos-notification-notice",
            message: "VIATURA NECESSITANDO AJUDA!",
            description: `Viatura ${message.prefix}, ${message.model} placa ${message.licensePlate} solicitou SOS!`,
            key: message.vehicleId,
        })

    }

    onReceiveCancelSOS(message: ReceiveCancelSOSVehicleMessage) {

        notification.close(message.vehicleId);

        if(message.vehicleSOS.length === 0){
            globalStore.dispatch(setOperationMode(SystemOperationModeType.NORMAL))
        }
    }

}

export default new SynchronizeService()