import React, { useEffect, useState } from 'react'
import { iconCarSOS } from '../Icons/Icons'
import { LocationMarkerType, ReceiveLocationVehicleMessage } from '../../../../../../hub/Notifications/models/ReceiveLocationVehicleMessage'
import { Marker, Popup } from 'react-leaflet'
import { NotificationHub } from '../../../../../../hub/Notifications/NotificationHub'
import { ReceiveCancelSOSVehicleMessage } from '../../../../../../hub/Notifications/models/ReceiveCancelSOSVehicleMessage'
import { VehicleMarkersProps } from './VehicleMarkers.types'

const VehicleMarkers: React.FC<VehicleMarkersProps> = (props) => {

    const [positions, setPositions] = useState<ReceiveLocationVehicleMessage[]>([])

    useEffect(() => {
        const keyLocation = NotificationHub.onReceiveLocation((message) => handlerLocationMessage(message));
        const keyCancel = NotificationHub.onReceiveCancelSOS((message) => handlerCancel(message));
        const keyNeed = NotificationHub.onReceiveNeedSOS((message) => console.log("Preciam de SOS"));


        return () => {
            NotificationHub.unRegister(keyLocation);
            NotificationHub.unRegister(keyCancel);
            NotificationHub.unRegister(keyNeed);
        }
    }, [])

    useEffect(() => {
        setPositions(props.vehicles);
    }, [props.vehicles])

    function handlerCancel(message: ReceiveCancelSOSVehicleMessage) {
        setPositions(old => [...old.filter(e => e.vehicleId !== message.vehicleId)])
    }

    function handlerLocationMessage(message: ReceiveLocationVehicleMessage) {

        if (message.type === LocationMarkerType.VEHICLE_SOS || message.type === LocationMarkerType.VEHICLE) {

            setPositions(old => [...old.filter(e => e.vehicleId !== message.vehicleId), message])

        }
    }

    return (
        <>
            {positions?.map(position => (
                <Marker
                    icon={iconCarSOS}
                    key={position.vehicleId}
                    position={{
                        lat: position.latitude,
                        lng: position.longitude
                    }}>
                    <Popup >
                        SOS!
                    </Popup>
                </Marker>
            ))}
        </>
    )
}

export default VehicleMarkers
