import React, { useCallback, useEffect, useState } from 'react'
import { Input } from '../../../../components/form/fields/WithFormItem'
import { Col, message, Modal, Row, Select as SelectAntd } from 'antd'
import { BadgesTypes, CreateUserCommand, UpdateUserCommand } from '../../models/commands/CreateUser'
import { EventService } from '../../../../services/Utils/event/event.service'
import { EventType } from '../../../../services/Utils/event/event.types'
import { FormCreateUserParm } from './FormCreateUser.types'
import { Select } from './../../../../components/form/fields/WithFormItem';
import { useForm } from 'react-hook-form'
import { useHistory, useParams } from 'react-router-dom'
import { UserApi } from '../../api/UserApi'
import CreateUserSchema from './CreateUserSchema'
import Form from '../../../../components/form/form/Form'

import "./FormUser.less"
const Option = SelectAntd.Option;

const FormCreateUser: React.FC = () => {

    const { userId } = useParams<FormCreateUserParm>();
    const [loading, setLoading] = useState(false);
    const form = useForm({ resolver: CreateUserSchema });

    const { push } = useHistory();

    useEffect(() => {
        if (userId) {
            setLoading(true);
            UserApi.getUser(userId)
                .then(data => {
                    form.reset(data.data)
                })
                .finally(() => setLoading(false))
        }

    }, [])

    async function onSubmit(value: CreateUserCommand | UpdateUserCommand) {
        setLoading(true);

        try {
            if (userId) {
                await UserApi.update({ ...value, id: userId } as UpdateUserCommand);
                push("/manage-users");
                EventService.trigger(EventType.update_user, value)
            } else {
                const result = await UserApi.create(value);
                push("/manage-users");
                message.success(`Usuário cadastrado com sucesso! login: ${result.data?.userName} senha: ${result.data?.password}`, 100)
                EventService.trigger(EventType.new_user, { ...value, id: result.data })
            }
        }
        catch (e) {
            message.error(JSON.stringify(e))
        }
        finally {
            setLoading(false);
        }
    }

    const handleKeyUp = useCallback(({ currentTarget }: React.FormEvent<HTMLInputElement>) => {

        var inputName = currentTarget.name;

        switch (inputName) {
            case "cep":

                break;

            default:
                break;
        }

        currentTarget.maxLength = inputName === "cep" ? 9 /* CPF */ :
            inputName === "birthDate" ? 10 /* Nascimento */ :
                inputName === "commercial" ? 14 /* Comercial */ :
                    inputName === "rg" ? 12 /* RG */ :
                        inputName === "cpf" ? 14 /* CPF */ :
                            15 /* Phone numbers */;

        var value = currentTarget.value.replace(/\D/g, "");

        switch (inputName) {
            case "cep":
                value = value.replace(/^(\d{5})(\d)/, "$1-$2");
                break;
            case "birthDate":
                value = value.replace(/^(\d{2})(\d{2})(\d)/, "$1/$2/$3");
                break;
            case "commercial":
                value = value.replace(/^(\d{2})(\d{4})(\d)/, "($1) $2-$3");
                break;
            case "rg":
                value = value.replace(/^(\d{1,2})(\d{3})(\d{3})(\d)/, "$1.$2.$3-$4");
                break;
            case "cpf":
                value = value.replace(/^(\d{3})(\d{3})(\d{3})(\d)/, "$1.$2.$3-$4");
                break;
            default:
                value = value.replace(/^(\d{2})(\d{5})(\d)/, "($1) $2-$3");
                break;
        }

        form.setValue(inputName as any, value);
    }, [form]);

    return (
        <>
            {console.log(form)}
            <Modal
                title="Cadastro de usuário"
                visible
                width="900px"
                okText="Salvar"
                cancelText="Cancelar"
                onOk={form.handleSubmit(onSubmit, err => console.log(err))}
                onCancel={() => { push("/manage-users") }}
            >
                <Form provider={form} loading={loading}>
                    <Row>
                        <Col span={8} >
                            <Input label="Nome" name="name" required />
                        </Col>
                        <Col span={8} >
                            <Input label="Nome de guerra" name="warName" required />
                        </Col>
                        <Col span={8} >
                            <Input label="Usuário do sistema" name="userName" required disabled={!!userId}/>
                        </Col>
                    </Row>


                    <Row>
                        <Col span={8} >
                            <Input label="RG" name="rg" onKeyUp={handleKeyUp} required />
                        </Col>
                        <Col span={8} >
                            <Input label="CPF" name="cpf" onKeyUp={handleKeyUp} required />
                        </Col>
                        < Col span={8} >
                            <Input label="Celular" name="phoneNumber" onKeyUp={handleKeyUp} required />
                        </Col>
                    </Row>
                    <Row>
                        <Col span={8} >
                            <Input label="E-mail" name="email" required />
                        </Col>
                        <Col span={8} >
                            <Select label="Graduação" name="badge" required >
                                <Option key={BadgesTypes.SOLDIER} value={BadgesTypes.SOLDIER}>Soldado</Option>
                                <Option key={BadgesTypes.CABLE} value={BadgesTypes.CABLE}>Cabo</Option>
                                <Option key={BadgesTypes.THIRD_SERGEANT} value={BadgesTypes.THIRD_SERGEANT}>Terceiro sargento</Option>
                                <Option key={BadgesTypes.SECOND_SERGEANT} value={BadgesTypes.SECOND_SERGEANT}>Segundo sargento</Option>
                                <Option key={BadgesTypes.FIRST_SERGEANT} value={BadgesTypes.FIRST_SERGEANT}>Primeiro sargento</Option>
                                <Option key={BadgesTypes.SERGEANT} value={BadgesTypes.SERGEANT}>Sargento</Option>
                                <Option key={BadgesTypes.OFFICER_CANDIDATE} value={BadgesTypes.OFFICER_CANDIDATE}>Aspirante a oficial</Option>
                                <Option key={BadgesTypes.SECOND_LIEUTENANT} value={BadgesTypes.SECOND_LIEUTENANT}>Segundo tenente</Option>
                                <Option key={BadgesTypes.FIRST_LIEUTENANT} value={BadgesTypes.FIRST_LIEUTENANT}>Primeiro tenente</Option>
                                <Option key={BadgesTypes.CAPTAIN} value={BadgesTypes.CAPTAIN}>Capitão</Option>
                                <Option key={BadgesTypes.MAJOR} value={BadgesTypes.MAJOR}>Major</Option>
                                <Option key={BadgesTypes.LIEUTENANT_COLONEL} value={BadgesTypes.LIEUTENANT_COLONEL}>Tenente coronel</Option>
                                <Option key={BadgesTypes.COLON} value={BadgesTypes.COLON}>Coronel</Option>
                            </Select>
                        </Col>
                        <Col span={8} >
                            <Input label="Função" name="occupation" required />
                        </Col>
                    </Row>

                </Form>
            </Modal>

        </>
    )
}

export default FormCreateUser


