import { SystemState, SystemOperationModeType } from "./System.types";

export const Types = {
    OPERATION_MODE: 'SYSTEM/OPERATION_MODE',
};

const initialState: SystemState = {
    operationMode: SystemOperationModeType.NORMAL
};

export default function reducer(state = initialState, action: any): SystemState {
    switch (action.type) {
        case Types.OPERATION_MODE:
            return { ...state, ...action.payload }
        default:
            return state;
    }
}


export function setOperationMode(operationMode: SystemOperationModeType) {
    return {
        type: Types.OPERATION_MODE,
        payload: {
            operationMode
        }
    }
}