import api from '../../../api/Api.configure';
import { ApiBase } from '../../../api/Api';
import { CreatePoliceComplaintCommand, CreatePoliceComplaintResult } from '../models/commands/CreatePoliceComplaintCommand';
import { RequestResponse } from '../../../models/RequestResponse';
import { CreatePoliceDelationCommand, CreatePoliceDelationResult } from '../models/commands/CreatePoliceDelationCommand';

export const endPoint: string = 'api/position-report';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async SaveComplaint(command: CreatePoliceComplaintCommand): Promise<RequestResponse<CreatePoliceComplaintResult>> {
        return (await api.post(`${endPoint}/police-complaint`, command)).data
    }

    async SaveDelation(command: CreatePoliceDelationCommand): Promise<RequestResponse<CreatePoliceDelationResult>> {
        return (await api.post(`${endPoint}/police-delation`, command)).data
    }
}
export const PositionReportApi = new Api();