import React, { useEffect, useState } from 'react'
import { Col, Divider, Modal, Row } from 'antd'
import { Input, TextArea } from '../../../../components/form/fields/WithFormItem';
import { IViewApproachReport } from './ViewApproachReport.types';
import { LogbookApi } from '../../api/LogbookApi';
import { useForm } from 'react-hook-form';
import { useHistory, useParams } from 'react-router-dom';
import Form from '../../../../components/form/form/Form';
import FormatDateServices from '../../../../services/Utils/FormatDate/FormatDateServices';

const ViewApproachReport: React.FC = () => {

    const { goBack } = useHistory()
    const { reportApproachesId } = useParams<IViewApproachReport>()
    const [loading, setLoading] = useState<boolean>(false);
    const form = useForm();

    useEffect(() => {

        setLoading(true)
        LogbookApi.getApproach(reportApproachesId)
            .then(date => form.reset(date.data))
            .finally(() => setLoading(false))
    }, [reportApproachesId])

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="900px"
            title={`Visualizar relatório de abordagens`}
            onCancel={() => { goBack() }}
            onOk={() => { goBack() }}
        >

            <Form provider={form} loading={loading} >
                <div className="view-accepted-reports-body">
                    <Row>
                        <Col span={8}>
                            <Input label="Equipe" name="team" readOnly />
                        </Col>
                        <Col span={4}>
                            <Input label="Prefico veículo" name="prefix" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={5}>
                            <Input label="Horário Inicial" name="startTime" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>
                        <Col span={5}>
                            <Input label="Horário final" name="endTime" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>
                        <Col span={5}>
                            <Input label="KM Inicial" name="startKm" readOnly />
                        </Col>
                        <Col span={5}>
                            <Input label="KM final" name="endkm" readOnly />
                        </Col>
                        <Col span={4}>
                            <Input label="KM total" name="totalkm" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={8}>
                            <Input label="Motorista" name="driverName" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="Comandante" name="CommanderName" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="3º Homem" name="thirdManName" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={8}>
                            <Input label="4º Homem" name="fourthManName" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="5º Homem" name="fifthManName" readOnly />
                        </Col>
                    </Row>


                    <Divider orientation="left">Abordagens</Divider>

                    <Row>
                        <Col span={8}>
                            <Input label="Abordagens de veículos" name="approachVehicle" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="Abordagens de motos" name="approachMoto" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="Abordagens de caminhões" name="approachTruck" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={8}>
                            <Input label="Abordagens de ônibus" name="approachBus" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="Abordagens de pedestres" name="approachPedestrian" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="Abordagens de passageiros" name="approachPassenger" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col offset={16} span={8}>
                            <Input label="Total de Abordagens" name="totalApproach" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col sm={24} md={24} lg={12}>
                            <TextArea label="Outras abordagens" rows={4} name="approach" readOnly />
                        </Col>
                        <Col sm={24} md={24} lg={12}>
                            <TextArea label="Descrição" rows={4} name="Descriptions" readOnly />
                        </Col>
                    </Row>
                </div>

            </Form>
        </Modal>
    )
}

export default ViewApproachReport
