import { LatLng, LatLngLiteral } from 'leaflet';
import React, { useCallback, useState } from 'react'
import { MapContextAction } from './MapContextProvider.types';

const MapContext = React.createContext<MapContextAction>({ selectedPosition: undefined, setSelectedPosition: () => { }, removeSelectedPosition: () => { } });

const MapContextProvider: React.FC = (props) => {

    const [selectedPosition, setSelectedPosition] = useState<LatLngLiteral>()

    const removeSelectedPosition = useCallback(
        () => {
            setSelectedPosition(undefined);
        }, [setSelectedPosition])

    return (
        <MapContext.Provider value={{ setSelectedPosition, selectedPosition, removeSelectedPosition }}  >
            {props.children}
        </MapContext.Provider>
    )
}

export { MapContext };
export default MapContextProvider