import React, { useEffect, useMemo, useState } from 'react'
import { Button, Table } from 'antd';
import { Link, useHistory } from 'react-router-dom';
import { PlusOutlined } from '@ant-design/icons';
import { BadgesTypes, ListUsersResult } from '../../models/queries/ListUsersResult';
import { UserApi } from '../../api/UserApi';
import { EventService } from '../../../../services/Utils/event/event.service';
import { EventType } from '../../../../services/Utils/event/event.types';
import { ColumnsType } from 'antd/lib/table';
import MenuTablet from '../../../../components/table/MenuTable/MenuTable';
import "./ListUsers.less"

const ListUsers: React.FC = () => {

    const { push } = useHistory();
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState<ListUsersResult[]>();

    const colunas = useMemo(() => [
        {
            title: 'Graduação',
            dataIndex: 'badge',
            key: 'badge',
            render: (value) => {
                switch (value) {
                    case BadgesTypes.SOLDIER: return "Soldado"
                    case BadgesTypes.CABLE: return "Cabo"
                    case BadgesTypes.THIRD_SERGEANT: return "Terceiro sargento"
                    case BadgesTypes.SECOND_SERGEANT: return "Segundo sargento"
                    case BadgesTypes.FIRST_SERGEANT: return "Primeiro sargento"
                    case BadgesTypes.SERGEANT: return "Sargento"
                    case BadgesTypes.OFFICER_CANDIDATE: return "Aspirante a oficial"
                    case BadgesTypes.SECOND_LIEUTENANT: return "Segundo tenente"
                    case BadgesTypes.FIRST_LIEUTENANT: return "Primeiro tenente"
                    case BadgesTypes.CAPTAIN: return "Capitão"
                    case BadgesTypes.MAJOR: return "Major"
                    case BadgesTypes.LIEUTENANT_COLONEL: return "Tenente coronel"
                    case BadgesTypes.COLON: return "Coronel"
                    default:
                        break;
                }
            }
        },
        {
            title: 'Nome',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: 'Nome de guerra',
            dataIndex: 'warName',
            key: 'warName',
        },
        {
            title: 'Celular',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: 'E-mail',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'Função',
            dataIndex: 'occupation',
            key: 'occupation',
        },
        {
            title: 'Ações',
            key: 'cancelate',
            width: 100,
            render: (value, record) => <MenuTablet menu={[{ name: "Editar", onClick: () => push(`/manage-users/edit/${record.id}`) }]} />
        }
    ] as ColumnsType<ListUsersResult>, []);


    useEffect(() => {
        onListUser();

        const cancelationNew = EventService.listen(EventType.new_user, () => onListUser());
        const cancelationUpdate = EventService.listen(EventType.update_user, () => onListUser());

        return () => {
            cancelationNew();
            cancelationUpdate();
        }
    }, []);

    async function onListUser() {
        setLoading(true)
        try {
            let result = await UserApi.listAll();
            setData(result.data)
        }
        finally {
            setLoading(false)
        }
    }

    const DraggableBodyRow = (props: any) => {

        if (props?.record?.update) {
            return <tr {...props} style={{ background: "#FAD24A" }} />;
        }

        return <tr {...props} />;
    };

    return (
        <>
            <div className="actions-box">
                <div></div>

                <Link to="/manage-users/new">
                    <Button type="primary" icon={<PlusOutlined />} size="large">
                        NOVO
                    </Button>
                </Link>
            </div>

            <Table rowKey="id" columns={colunas} dataSource={data} size="middle"
                components={{
                    body: {
                        row: DraggableBodyRow,
                    }
                }}
                loading={loading}
            />
        </>
    )
}

export default ListUsers
