import { Modal } from 'antd'
import React, { useEffect, useState } from 'react'
import { useForm } from 'react-hook-form'
import { useHistory, useParams } from 'react-router-dom'
import Form from '../../../../components/form/form/Form'
import { LoogbookApi } from '../../api/LoogbookApi'
import { DetailsFillQuestionResult } from '../../models/queries/DetailsFillQuestion'
import FillFormCheckPartResume from '../history-check-parts/components/FillForm/FillFormCheckPartResume'
import FillFormGeneral from '../history-check-parts/components/FillForm/FillFormGeneral'

const DetailCheckParts: React.FC = () => {

    const { fillQuestionsId } = useParams<{ fillQuestionsId: string }>()
    const { goBack } = useHistory();
    const [loading, setLoading] = useState(false);
    const form = useForm<DetailsFillQuestionResult>();

    useEffect(() => {
        getFill(fillQuestionsId)
    }, [])

    function getFill(id: string) {
        setLoading(true);

        LoogbookApi.details(id)
            .then(result => {
                form.reset(result.data)
            })
            .finally(() => {
                setLoading(false);
            })
    }

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="70%"
            title="Historico diário de bordo"
            onCancel={() => { goBack() }}
            onOk={() => { goBack() }}
        >
            <Form provider={form} loading={loading}>

                <FillFormGeneral />
                <FillFormCheckPartResume />

            </Form>
        </Modal>
    )
}

export default DetailCheckParts
