import React from 'react'
import { Route } from 'react-router-dom';
import CancelateReport from './pages/cancelate-report/CancelateReport';
import ListAcceptedReports from './pages/list-accepted-reports/ListAcceptedReports';
import ListApproachReports from './pages/list-approach-reports/ListApproachReports';
import UpdatePoliceComplaint from './pages/update-report/update-report-complaint/UpdatePoliceComplaint';
import UpdatePoliceDelation from './pages/update-report/update-report-delation/UpdatePoliceDelation';
import ViewAcceptedReport from './pages/view-accepted-report/ViewAcceptedReport';
import ViewApproachReport from './pages/view-approach-report/ViewApproachReport';

const Routes: React.FC = () => {

    return (
        <>
            <Route path="/accepted-reports" component={ListAcceptedReports} />
            <Route path="/accepted-reports" component={ListApproachReports} />

            <Route path="/accepted-reports/:positionReportId" exact component={ViewAcceptedReport} />
            <Route path="/accepted-reports/cancel/:positionReportId" component={CancelateReport} />
            <Route path="/map-2d/report/:positionReportId" component={ViewAcceptedReport} />

            <Route path="/accepted-reports/edit-delation/:positionReportId" component={UpdatePoliceDelation} />
            <Route path="/accepted-reports/edit-complaint/:positionReportId" component={UpdatePoliceComplaint} />

            <Route path="/accepted-reports/report-approaches/:reportApproachesId" exact component={ViewApproachReport} />
        </>
    )
}

export default Routes