import { notification } from "antd";
import { BootstrapApi } from "../../api/Bootstraps/BootstrapApi"
import { OperationModeType } from "../../api/Bootstraps/models/GetStateResult";
import { globalStore } from "../../stores/global-store";
import { setOperationMode } from "../../stores/System/System.store";
import { SystemOperationModeType } from "../../stores/System/System.types";

export class BootstrapService {


    async init() {
        let result = await BootstrapApi.GetState();

        if (result.data?.operationMode === OperationModeType.IN_SOS) {
            globalStore.dispatch(setOperationMode(SystemOperationModeType.IN_SOS))
        }
        
        result.data?.vehicleNeedSOS.forEach(vehicle =>{
            notification.error({
                duration: 100,
                className: "need-sos-notification-notice",
                message: "VIATURA NECESSITANDO AJUDA!",
                description: `Viatura ${vehicle.model} placa ${vehicle.licensePlate} solicitou SOS!`,
                key: vehicle.vehicleId,
            })
        })
    }

    dispose() {
    }

}

export default new BootstrapService()