import React, { useEffect, useState } from 'react'
import { BrandApi } from '../../api/BrandApi';
import { Col, Modal, Row, message, Select as SelectAntd } from 'antd'
import { CreateVehicleCommand, FuelType } from '../../models/commands/CreateVehicle';
import { EventService } from '../../../../services/Utils/event/event.service';
import { EventType } from '../../../../services/Utils/event/event.types';
import { Input, Select, TextArea } from '../../../../components/form/fields/WithFormItem';
import { ListBrandResult } from '../../models/queries/ListBrand';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import { VehicleApi } from '../../api/VehicleApi';
import CreateVehicleSchema from './CreateVehicleSchema';
import Form from '../../../../components/form/form/Form';

import "./FormVehicle.less"

const FormPoliceCar: React.FC = () => {

    const [loading, setLoading] = useState(false);
    const [brands, setBrands] = useState<ListBrandResult[]>([])

    const form = useForm({ resolver: CreateVehicleSchema });
    const { push } = useHistory();

    useEffect(() => {
        BrandApi.listAll().then(list => setBrands(list.data ?? []))

    }, [])

    async function onSubmit(value: CreateVehicleCommand) {
        setLoading(true);

        try {

            const result = await VehicleApi.Create(value);
            push("/check-parts");
            message.success("Veículo cadastrado com sucesso!")
            EventService.trigger(EventType.new_vehicle, { ...value, id: result.data })
        }
        catch (e) {
            message.error(JSON.stringify(e))
        }
        finally {
            setLoading(false);
        }
    }

    return (
        <Modal
            title="Cadastro de viatura"
            okText="Salvar"
            onOk={form.handleSubmit(onSubmit)}
            onCancel={() => { push("/check-parts") }}
            visible
            width="900px"
            className="form-police-car-modal"
        >
            <Form provider={form} loading={loading}>
                <Row>
                    <Col xs={12} sm={6} md={5}>
                        <Input label="Prefixo" name="prefix" required />
                    </Col>
                    <Col xs={12} sm={6} md={5}>
                        <Input label="Placa" name="licensePlate" required />
                    </Col>
                    <Col xs={12} sm={6} md={5} >
                        <Select label="Marca" name="brandId" required  >
                            {brands.map(e => <SelectAntd.Option value={e.id}>{e.name}</SelectAntd.Option>)}
                        </Select>
                    </Col>
                    <Col xs={12} sm={6} md={5} >
                        <Input label="Modelo" name="model" required />
                    </Col>
                    <Col xs={12} sm={5} md={4} >
                        <Input label="Cor" name="color" required />
                    </Col>
                    <Col xs={12} sm={6} md={5} >
                        <Input label="Ano/Modelo" name="year" required />
                    </Col>
                    <Col xs={12} sm={6} md={5} >
                        <Input label="KM inicial" name="initialKm" required />
                    </Col>
                    <Col xs={12} sm={6} md={5} >
                        <Select label="Combustível" name="fuel" required  >
                            <SelectAntd.Option value={FuelType.Gasoline_Alcohol}>Flex</SelectAntd.Option>
                            <SelectAntd.Option value={FuelType.Gasoline}>Gasolina</SelectAntd.Option>
                            <SelectAntd.Option value={FuelType.Alcohol}>Álcool</SelectAntd.Option>
                            <SelectAntd.Option value={FuelType.Diesel}>Diesel</SelectAntd.Option>
                        </Select>
                    </Col>
                </Row>

                <Row>
                    <Col xs={24} sm={12} md={8} >
                        <Input label="Renavam" name="renavam" required />
                    </Col>
                    <Col xs={24} sm={12} md={8} >
                        <Input label="Chassi" name="chassi" required />
                    </Col>
                </Row>

                <Row>
                    <Col xs={24} md={12} >
                        <TextArea label="Detalhes" name="Details" rows={4} />
                    </Col>
                    <Col xs={24} md={12} >
                        <TextArea label="Descrição" name="Description" rows={4} />
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}

export default FormPoliceCar
