
import api from '../../../api/Api.configure';
import { RequestResponse } from '../../../models/RequestResponse';
import { ApiBase } from '../../../api/Api';
import { ListDeviceResult } from '../models/queries/ListDevice';
import { DefineVehicleCommand } from '../models/commands/DefineVehicle';

export const endPoint: string = 'api/devices';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async defineVehicle(command: DefineVehicleCommand): Promise<RequestResponse<any>> {
        return (await api.post(`${endPoint}/define-vehicle`, command)).data
    }

    async listAll(): Promise<RequestResponse<ListDeviceResult[]>> {
        return (await api.get(`${endPoint}/list-all`)).data
    }


}
export const DeviceApi = new Api();