import { BackTop, Col, Collapse, Divider, Row } from 'antd'
import React, { useMemo } from 'react'
import { useWatch } from 'react-hook-form';
import { Input } from '../../../../../../components/form/fields/WithFormItem';
import { DetailsLastFillQuestionResult_FormQuestion, QuestionAnswerType, QuestionType } from '../../../../models/queries/DetailsLastFillQuestion';

const { Panel } = Collapse;

const FillFormCheckPartResume: React.FC = () => {

    const formQuestions = useWatch<DetailsLastFillQuestionResult_FormQuestion[]>({ name: "formQuestions" });

    const memoizedValue: Record<string, boolean> = useMemo(() => {
        let keys = {}
        if (!formQuestions) {
            return {}
        }

        for (let index = 0; index < formQuestions.length; index++) {
            let record = formQuestions[index];

            keys = {
                ...keys,
                [record.id]: record.questions.some(e => e.answers.some(ee => ee.isSelected && ee.type === QuestionAnswerType.Bad))
            }
        }

        return keys;

    }, [formQuestions])

    return (
        <div >
            <Divider orientation="left">Check list</Divider>

            <Collapse >

                {formQuestions?.map((field, index) => (
                    <Panel header={field.title} key={field.id} style={memoizedValue[field.id] ? { background: "#FF0000B0" } : {}}>
                        <CheckListPart {...field} />
                    </Panel>))}

            </Collapse>

            <BackTop>
                <div >Subir</div>
            </BackTop>
        </div >
    )
}

export default FillFormCheckPartResume;

const CheckListPart: React.FC<DetailsLastFillQuestionResult_FormQuestion> = (props) => {
    return (
        <Row>
            {props.questions.map(e => {
                let answer = e.answers.find(e => e.isSelected);

                return (<Col sm={24} md={12} key={answer?.key}>
                    <Input label={e.description}
                        name={e.id}
                        value={answer?.description} readOnly
                        style={answer?.type === QuestionAnswerType.Bad ? { borderColor: "red", color: "red" } : {}} />
                </Col>)
            })}
        </Row>
    )
}

