export interface FillQuestionMessage {
    id: string;
    identifierVehicle: string;
    licensePlate: string;
    model: string;
    brand: string;
    color: string;
    year: number;
    lastFillQuestion: string | null;
    nextMaintenance: string | null;
    situation: VehiclesSituationType;
}

export enum VehiclesSituationType {
    OK,
    BAD,
    NEW,
    STOPPED,
    CHANGE_OIL,
    NEAR_CHANGE_OIL
}