import React, { useEffect, useState } from 'react'
import { AcceptedReportApi } from '../../../api/AcceptedReportApi';
import { Col, message, Modal, Row, Select as SelectAntd} from 'antd'
import { useForm } from 'react-hook-form';
import { HttpStatusCode } from '../../../../../models/RequestResponse';
import { DatePicker, Input, TextArea, Select, Switch } from '../../../../../components/form/fields/WithFormItem';
import { useHistory, useParams } from 'react-router-dom';
import Form from '../../../../../components/form/form/Form';
import FormatDateServices from '../../../../../services/Utils/FormatDate/FormatDateServices';
import { UpdatePoliceDelationCommand } from '../../../models/commands/UpdatePoliceDelationCommand';
import { DayOfWeek } from '../../../models/common/CommonPoliceReport';
import UpdatePoliceDelationSchema from './UpdatePoliceDelationSchema';
import "./UpdatePoliceDelation.less";
import moment from 'moment';

const UpdatePoliceDelation: React.FC = () => {

    const { Option } = SelectAntd;
    const { Sunday, Monday, Tuesday, Wednesday, Thursday, Friday, Saturday } = DayOfWeek;
    
    const { positionReportId } = useParams<{ positionReportId: string }>()
    const { goBack } = useHistory()
    const [loading, setLoading] = useState(false);
    const form = useForm({ resolver: UpdatePoliceDelationSchema });


    useEffect(() => {

        getReportDelation(positionReportId);

    }, []);

    async function onSubmit(value: UpdatePoliceDelationCommand) {
        setLoading(true);

        try {
                await AcceptedReportApi.updatePoliceDelation({...value, id: positionReportId } as UpdatePoliceDelationCommand);
                message.success("Denúncia atualizada com sucesso!");
                goBack();
            }
        catch (e) {
            message.error("Erro crítico.")
        }
        finally {
            setLoading(false);
        }
    }

    function getReportDelation(id: string) {
        setLoading(true);

        AcceptedReportApi.getReportDelation(id)
            .then(result => {
                if (result.status === HttpStatusCode.NotFound) {
                    message.info("Denúncia inválida")
                    goBack();
                }
                form.reset({ ...result.data, dateTime: moment(result.data?.dateTime) });
            })
            .finally(() => {
                setLoading(false);
            }
        );
    }

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="900px"
            title={`Editar denúncia`}
            onCancel={() => { goBack() }}
            onOk={form.handleSubmit(onSubmit, err => console.log(err))}
        >
            <Form provider={form} loading={loading} >
                <div className="update-police-delation-body">
                    <Row>
                        <Col span={6}>
                            <Input label="Código da Ocorrência" name="code" readOnly />
                        </Col>

                        <Col span={6}>
                            <Input label="Criado em" name="registedAt" render={(value: Date) => FormatDateServices.formatDateTime(value)} readOnly />
                        </Col>

                        <Col span={4}>
                            <Switch label="Anônimo?" name="isAnonymus" checkedChildren={"Sim"} unCheckedChildren={"Não"} />
                        </Col>

                        <Col span={8}>
                            <Input label="Denunciante" name="whistleblower" required={!form.getValues().isAnonymus} />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={24}>
                            <TextArea label="Detalhes" rows={6} name="description" required />
                        </Col>
                    </Row>

                    <Row>
                        
                        <Col span={8}>
                            <DatePicker
                                label="Data da ocorrência"
                                name="dateTime"
                                format="DD/MM/yyyy HH:mm:ss"
                                showTime
                                required
                            />
                        </Col>
                        
                        <Col span={16} >
                            <Select label="Dias da semana" name="daysOfWeek" mode="multiple"  >
                                <Option value={Sunday}>Domingo</Option>
                                <Option value={Monday}>Segunda</Option>
                                <Option value={Tuesday}>Terça</Option>
                                <Option value={Wednesday}>Quarta</Option>
                                <Option value={Thursday}>Quinta</Option>
                                <Option value={Friday}>Sexta</Option>
                                <Option value={Saturday}>Sabado</Option>
                            </Select>
                        </Col>
                    </Row>
                    
                    <Row>
                        <Col span={16}>
                            <Input label="Cidade" name="city" value="city" />
                        </Col>
                        <Col span={8}>
                            <Input label="Bairro" name="district" value="district" />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={12}>
                            <Input label="Rua" name="street" value="street" />
                        </Col>
                        <Col span={6}>
                            <Input label="Número" name="number" value="number" />
                        </Col>
                        <Col span={6}>
                            <Input label="CEP" name="zipCode" value="zipCode" />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={12}>
                            <Input label="Complemento" name="complement" />
                        </Col>

                        <Col span={12}>
                            <Input label="Tipo de localidade" name="locationType" />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={12}>
                            <TextArea label="Ponto de referência" name="referencePoint" />
                        </Col>

                        <Col span={12}>
                            <TextArea label="Características do local" name="locationFeatures" />
                        </Col>
                    </Row>
                </div>

            </Form>
        </Modal>
    )
}

export default UpdatePoliceDelation;