import React from 'react';
import { Route } from 'react-router-dom';
import * as services from "./services/Index"
import CheckParts from "./modules/check-parts/Routes"
import ManageUsers from "./modules/manage-users/Routes"
import AcceptedReports from "./modules/accepted-reports/Routes"
import Map2dContainer from "./modules/map-2d/Routes"

services.init();

const Routes: React.FC = () => {
    return (
        <>
            <Route path="/" exact component={Map2dContainer} />
            <Map2dContainer />
            <Route path="/check-parts" component={CheckParts} />
            <Route path="/manage-users" component={ManageUsers} />
            <AcceptedReports />
        </>
    );
}

export default Routes;
