import React, { useCallback, useContext, useEffect, useRef, useState } from 'react'
import { CreatePoliceComplaintCommand } from '../../../../models/commands/CreatePoliceComplaintCommand';
import { CreatePoliceDelationCommand } from '../../../../models/commands/CreatePoliceDelationCommand';
import { iconSelectedPosition } from '../Icons/Icons';
import { MapContext } from '../MapContextProvider/MapContextProvider';
import { Marker, Popup, useMap } from 'react-leaflet';
import { PoliceComplaintHandles } from '../../../PoliceComplaint/PoliceComplaint.types';
import { PoliceDelationHandles } from '../../../PoliceDelation/PoliceDelation.types';
import { ReportType } from '../../../../models/common/CommonPoliceReport';
import { Tabs } from 'antd';
import { TypeOf } from 'yup';
import Modal from 'antd/lib/modal/Modal'
import PoliceComplaint from '../../../PoliceComplaint/PoliceComplaint';
import PoliceDelation from '../../../PoliceDelation/PoliceDelation';

import "./SelectedPosition.less"

const SelectedPosition: React.FC = () => {

    const map = useMap();
    const refMarker = useRef<any>(null)
    const [loading, setLoading] = useState(false)
    const refDelation = useRef<PoliceDelationHandles>(null)
    const refComplaint = useRef<PoliceComplaintHandles>(null)
    const [selectReportType, setSelectReportType] = useState<ReportType>(ReportType.Complaint);
    const [showPosiceReport, setShowPosiceReport] = useState<boolean>(false);
    const { selectedPosition, removeSelectedPosition } = useContext(MapContext);
    const { TabPane } = Tabs;

    useEffect(() => {

        if (selectedPosition) {

            map.flyTo({
                lat: selectedPosition?.lat!,
                lng: selectedPosition?.lng!
            }, 18)

            refMarker.current && refMarker.current?.openPopup()
        }

    }, [map, selectedPosition])

    const onCancel = useCallback(() => {
        setShowPosiceReport(false);
    }, [])

    const onClose = useCallback(() => {
        setShowPosiceReport(false);
        removeSelectedPosition();

    }, [removeSelectedPosition])

    const onchangeReport = (type: ReportType) => {
        setSelectReportType(type);
        setShowPosiceReport(true);


        const complaint = refComplaint.current?.getValues();
        const delatation = refDelation.current?.getValues();

        if (type === ReportType.Complaint) {
            refComplaint.current?.setValues({ ...complaint, ...delatation } as CreatePoliceComplaintCommand);
        }

        if (type === ReportType.Delation) {
            refDelation.current?.setValues({ ...delatation, ...complaint } as CreatePoliceDelationCommand)

        }
    }

    const onSubmit = useCallback(async (e?: React.BaseSyntheticEvent) => {
        setLoading(true)

        try {
            if (selectReportType === ReportType.Complaint) {
                return await refComplaint.current?.handleSubmit(e)
            }

            return await refDelation.current?.handleSubmit(e)
        }
        finally {
            setLoading(false)
        }
    }, [selectReportType])

    if (!selectedPosition) {
        return null;
    }

    return (
        <>
            <Marker
                ref={refMarker}
                icon={iconSelectedPosition}
                key={selectedPosition.lat}
                position={selectedPosition}>
                <Popup className="selected-position-popup">
                    <div className="selected-position-container">
                        <div className="selected-position-title">Cadastro de Local</div>

                        <div className="selected-position-menu">
                            <div onClick={() => onchangeReport(ReportType.Complaint)} className="selected-position-option">Cadastro de Ocorrência</div>
                            <div onClick={() => onchangeReport(ReportType.Delation)} className="selected-position-option">Cadastro de Denúncia</div>
                        </div>

                    </div>
                </Popup>
            </Marker>


            <Modal title={`Cadastro de ${selectReportType === ReportType.Complaint ? "ocorrência" : "denúncia"}`}
                width={900}
                okText="Salvar"
                cancelText="Cancelar"
                onOk={onSubmit}
                visible={showPosiceReport}
                onCancel={() => onCancel()}
                okButtonProps={{ loading: loading }}
            >

                <Tabs
                    activeKey={selectReportType.toString()}
                    animated={{ inkBar: true, tabPane: true }}
                    onChange={key => onchangeReport(parseInt(key))}>

                    <TabPane tab="Ocorrência" key={ReportType.Complaint}>
                        <PoliceComplaint ref={refComplaint} onClose={onClose} latitude={selectedPosition.lat} longitude={selectedPosition.lng} />
                    </TabPane>

                    <TabPane tab="Denúncia" key={ReportType.Delation}>
                        <PoliceDelation ref={refDelation} onClose={onClose} latitude={selectedPosition.lat} longitude={selectedPosition.lng} />
                    </TabPane>

                </Tabs>
            </Modal>


        </>
    )
}

export default SelectedPosition
