import React from 'react'
import { Route } from 'react-router-dom';
import DetailCheckParts from './pages/detail-check-parts/DetailCheckParts';
import FormPoliceCar from './pages/form-vehicle/FormVehicle';
import HistoryCheckParts from './pages/history-check-parts/HistoryCheckParts';
import ListCheckParts from './pages/list-check-parts/ListCheckParts';
import SelectDevice from './pages/select-device/SelectDevice';

const Routes: React.FC = () => {

    return (
        <>
            <Route path="/check-parts" component={ListCheckParts} />
            <Route path="/check-parts/new" component={FormPoliceCar} />
            <Route path="/check-parts/history/:vehicleId" component={HistoryCheckParts} />
            <Route path="/check-parts/history/:vehicleId/detail/:fillQuestionsId" component={DetailCheckParts} />

            <Route path="/check-parts/select-device/:vehicleId" component={SelectDevice} />
        </>
    )
}

export default Routes
