import React from 'react'
import { Modal, Tabs } from 'antd'
import { useHistory } from 'react-router-dom';
import LastFillFormContainer from './components/LastFillForm/LastFillFormContainer';
import ListHistoricGeneral from './components/ListHistoric/ListHistoricContainer';

const { TabPane } = Tabs;

const HistoryCheckParts: React.FC = () => {

    const { goBack } = useHistory()

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="70%"
            title="Historico diário de bordo"
            onCancel={() => { goBack() }}
            onOk={() => { goBack() }}
        >
            <Tabs defaultActiveKey="1"
                tabPosition="top"
                size="small"
                animated={{ inkBar: true, tabPane: true }}
                destroyInactiveTabPane>

                <TabPane tab="Última ficha" key="1">
                    <LastFillFormContainer />
                </TabPane>
                <TabPane tab="Todas as fichas" key="2">
                    <ListHistoricGeneral />
                </TabPane>

            </Tabs>
        </Modal>
    )
}

export default HistoryCheckParts
