import React, { useEffect, useState } from 'react'
import { AllMarkersResult } from '../../models/queries/AllMarkers';
import { Map2dApi } from '../../api/Map2dApi';
import { MapContainer, TileLayer, ZoomControl, LayersControl, LayerGroup } from 'react-leaflet';
import { message } from 'antd';
import VehicleMarkers from './components/VehicleMarkers/VehicleMarkers';

import 'leaflet/dist/leaflet.css';
import "./map-2d.less"
import SelectedPosition from './components/SelectedPosition/SelectedPosition';
import PoliceReportMarker from './components/PoliceReportMarker/PoliceDelationMarker/PoliceDelationMarker';
import SearchAddress from './components/SearchAddress/SearchAddress';
import MapContextProvider from './components/MapContextProvider/MapContextProvider';
import PointSelectMouse from './components/PointSelectMouse/PointSelectMouse';
import PoliceComplaintMarker from './components/PoliceReportMarker/PoliceComplaintMarker/PoliceComplaintMarker';


const Map2d: React.FC = () => {

    const [markers, setMarkers] = useState<AllMarkersResult>({ vehicles: [], policeComplaintReports: [], policeDelationReports: [] })

    useEffect(() => {
        init();

    }, []);

    async function init() {
        try {

            let result = await Map2dApi.getAllMarkers();

            if (result.data) {
                setMarkers(result.data!);
            }
        }
        catch {
            message.error("Erro ao carregar as informações do mapa!")
        }
    }

    return (
        <MapContextProvider>
            <div className="leaflet-container">
                <MapContainer
                    zoom={13}
                    zoomControl={false}
                    style={{ height: "90vh" }}
                    center={{ alt: 1000, lat: -25.531909, lng: -54.566674, }}
                >
                    <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" zIndex={100} />
                    {/* <TileLayer url="http://mt0.google.com/vt/lyrs=s&x={x}&y={y}&z={z}" zIndex={100} /> */}
                    <ZoomControl position="topright" />


                    <VehicleMarkers vehicles={markers.vehicles} />
                    <PoliceComplaintMarker policeReports={markers.policeComplaintReports} />
                    <PoliceReportMarker policeReports={markers.policeDelationReports} />

                    <PointSelectMouse />
                    <SearchAddress />
                    <SelectedPosition />
                </MapContainer>
            </div>
        </MapContextProvider>
    )
}

export default Map2d
