import { Badge } from 'antd'
import React from 'react'
import { VehiclesSituationType } from '../../models/queries/ListVehicles'
import { SituationVehicleProps } from './SituationVehicle.types'

const SituationVehicle: React.FC<SituationVehicleProps> = ({ situation }) => {

    if (situation === VehiclesSituationType.OK) {
        return (<Badge status="success" text="OK" />)
    }

    if (situation === VehiclesSituationType.BAD) {
        return (<Badge status="error" text="Atenção" />)
    }

    if (situation === VehiclesSituationType.NEW) {
        return (<Badge status="success" text="Novo" />)
    }

    if (situation === VehiclesSituationType.STOPPED) {
        return (<Badge status="warning" text="Parado" />)
    }

    if (situation === VehiclesSituationType.CHANGE_OIL) {
        return (<Badge status="default" text="Troca de óleo" />)
    }

    if (situation === VehiclesSituationType.NEAR_CHANGE_OIL) {
        return (<Badge status="warning" text="Troca de óleo" />)
    }

    return (<Badge status="error" text="Atenção" />)
}

export default SituationVehicle
