import { HubBase } from "../Hub";
import { FillQuestionMessage } from "./models/FillQuestionMessage";

export const endPoint: string = 'hubs/fillquestion';

class Hub extends HubBase {

    constructor() {
        super(endPoint);
    }

    onReceiveMessage(newMethod: (args: FillQuestionMessage) => void): string {

        return super.register("ReceiveMessage", newMethod)

    }
}
export const FillQuestionHub = new Hub();