import React from 'react'
import { Badge } from 'antd'
import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    NotificationOutlined
} from '@ant-design/icons';
import { IBasicHeaderProps } from './BasicHeader.types';

import "./basic-header-styles.less"
import AvatarDropdown from './components/avatar-dropdown/AvatarDropdown';

const BasicHeader: React.FC<IBasicHeaderProps> = ({ collapsed, toggle }) => {
    return (
        <div className="basic-header-container">
            <div >
                {React.createElement(collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                    onClick: toggle, 
                    style: {  
                        color: 'grey', 
                        fontSize: '20px'
                    }
                })}
            </div>
            <div>
                <div style={{
                    display: "flex"
                }}>
                    <div style={{ paddingRight: 12 }} >
                        <Badge count={1} dot >
                            <NotificationOutlined />
                        </Badge>
                    </div>

                    <AvatarDropdown />
                </div>
            </div>
        </div>
    )
}

export default BasicHeader
