
import { RequestResponse } from '../../../models/RequestResponse';
import { ApiBase } from '../../../api/Api';
import api from '../../../api/Api.configure';
import { ListBrandResult } from '../models/queries/ListBrand';

export const endPoint: string = 'api/brands';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async listAll(): Promise<RequestResponse<ListBrandResult[]>> {
        return (await api.get(`${endPoint}/list-all`)).data
    }

}
export const BrandApi = new Api();