import React, { useEffect, useState } from 'react'
import { Col, Row } from 'antd'
import { iconPoliceDelationReport } from '../../Icons/Icons'
import { Marker, Popup } from 'react-leaflet'
import { NotificationHub } from '../../../../../../../hub/Notifications/NotificationHub'
import { PoliceDelationReportMarkerProps } from './PoliceDelationMarker.types'
import { ReceivePoliceDelationReportMessage } from '../../../../../../../hub/Notifications/models/ReceivePoliceDelationReportMessage'
import FormatDateServices from '../../../../../../../services/Utils/FormatDate/FormatDateServices'

import "./PoliceDelationMarker.less";
import { DayOfWeek } from '../../../../../models/common/CommonPoliceReport'

const PoliceDelationMarker: React.FC<PoliceDelationReportMarkerProps> = (props) => {

    const [positions, setPositions] = useState<ReceivePoliceDelationReportMessage[]>([])

    useEffect(() => {
        const keyLocation = NotificationHub.onReceivePoliceDelationReport((message) => handlerReceivePoliceReport(message));

        return () => {
            NotificationHub.unRegister(keyLocation);
        }
    }, [])

    useEffect(() => {
        setPositions(props.policeReports);
    }, [props.policeReports])


    function handlerReceivePoliceReport(message: ReceivePoliceDelationReportMessage) {
        setPositions(old => [...old, message]);
    }

    const convertToEnumFromWeekDay = (dayOfWeek: DayOfWeek) => {
        switch (dayOfWeek) {
            case DayOfWeek.Sunday: return "Domingo";
            case DayOfWeek.Monday: return "Segunda";
            case DayOfWeek.Tuesday: return "Terça";
            case DayOfWeek.Wednesday: return "Quarta";
            case DayOfWeek.Thursday: return "Quinta";
            case DayOfWeek.Friday: return "Sexta";
            case DayOfWeek.Saturday: return "Sabado";
        }
    }

    const getDaysOfTheWeek = (daysOfTheWeek: DayOfWeek[]) => {
        return daysOfTheWeek.sort().map(day => convertToEnumFromWeekDay(day)).toString().replace(",", ", ");
    }

    return (
        <>
            {positions?.map(position => (
                <Marker
                    icon={iconPoliceDelationReport}
                    key={position.policeReportId}
                    position={{
                        lat: position.latitude,
                        lng: position.longitude
                    }}>
                    <Popup className="police-delation-report-marker-popup">
                        <div className="police-delation-report-marker-container">
                            <div className="police-delation-report-marker-title">{position.code} - Denúncia</div>
                            <div className="police-delation-report-marker-body">
                                <Row>
                                    <Col span={12}>
                                        <span className="police-delation-report-marker-label">Data: </span>
                                        {FormatDateServices.formatDate(position.registedAt)}
                                    </Col>

                                    <Col span={12}>
                                        <span className="police-delation-report-marker-label">Hora: </span>
                                        {FormatDateServices.formatTime(position.registedAt)}
                                    </Col>
                                </Row>

                                <div>
                                    <span className="police-delation-report-marker-label">Solicitante: </span>
                                    {position.whistleblower ?? "Anônimo"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Usuário: </span>
                                    {"Administrador"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Data da ocorrência: </span>
                                    {FormatDateServices.formatDateTime(position.dateTime)}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Dias da semana: </span>
                                    {getDaysOfTheWeek(position.daysOfWeek) ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Detalhes: </span>
                                    {position.description ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Cidade: </span>
                                    {position.city ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Rua: </span>
                                    {position.street ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Bairro: </span>
                                    {position.district ?? "_"}
                                </div>

                                <Row>
                                    <Col span={12}>
                                        <span className="police-delation-report-marker-label">Número: </span>
                                        {position.number ?? "_"}
                                    </Col>
                                    <Col span={12}>
                                        <span className="police-delation-report-marker-label">CEP: </span>
                                        {position.zipCode ?? "_"}
                                    </Col>
                                </Row>

                                <div>
                                    <span className="police-delation-report-marker-label">Complemento: </span>
                                    {position.complement ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Tipo de localidade: </span>
                                    {position.locationType ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Ponto de referência: </span>
                                    {position.referencePoint ?? "_"}
                                </div>

                                <div>
                                    <span className="police-delation-report-marker-label">Características do local: </span>
                                    {position.locationFeatures ?? "_"}
                                </div>
                            </div>
                        </div>
                    </Popup>
                </Marker>
            ))}
        </>
    )
}

export default PoliceDelationMarker
