import React, { useEffect, useMemo, useState } from 'react'
import { Button, Table } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import { EventService } from '../../../../services/Utils/event/event.service';
import { EventType } from '../../../../services/Utils/event/event.types';
import { FillQuestionHub } from '../../../../hub/FillQuestionHub/FillQuestionHub';
import { FillQuestionMessage } from '../../../../hub/FillQuestionHub/models/FillQuestionMessage';
import { Link, useHistory } from 'react-router-dom';
import { ListVehiclesResult, VehiclesSituationType } from '../../models/queries/ListVehicles';
import { PlusOutlined } from '@ant-design/icons';
import { VehicleApi } from '../../api/VehicleApi';
import FormatDateServices from '../../../../services/Utils/FormatDate/FormatDateServices';
import SituationVehicle from '../../components/situation-vehicle/SituationVehicle';
import "./list-check-parts.less"


const ListCheckParts: React.FC = () => {

    const { push } = useHistory();
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState<ListVehiclesResult[]>()
    const colunas = useMemo(() => [
        {
            title: 'Prefixo',
            dataIndex: 'prefix',
            key: 'prefix',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Placa',
            dataIndex: 'licensePlate',
            key: 'licensePlate',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Modelo',
            dataIndex: 'model',
            key: 'model',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Marca',
            dataIndex: 'brand',
            key: 'brand',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Cor',
            dataIndex: 'color',
            key: 'color',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Ano',
            dataIndex: 'year',
            key: 'year',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Situação veículo',
            key: 'situation',
            dataIndex: "situation",
            width: "130px",
            onCell: (data) => ({ data }),
            render: (situation: VehiclesSituationType) => <SituationVehicle situation={situation} />
        },
        {
            title: 'Última ficha',
            key: 'brand',
            dataIndex: "lastFillQuestion",
            onCell: (data) => ({ data }),
            render: (date?: Date) => date ? FormatDateServices.formatDateTime(date) : "-"
        },
        {
            title: 'Tablet',
            key: 'deviceName',
            dataIndex: "deviceName",
            onCell: (data) => ({ data }),
            render: (date?: string) => date ?? "-"
        },
        {
            title: 'Selecionar tablet',
            key: 'tablet',
            dataIndex: "id",
            width: "150px",
            onCell: (data) => ({ tabletOption: true, data }),
            render: (date: string) => <Button>Selecionar tablet</Button>
        },
    ] as ColumnsType<ListVehiclesResult>, []);

    useEffect(() => {
        onListVehicle();

        const key = FillQuestionHub.onReceiveMessage((message) => {
            handlerNewFillQuestion(message);
        });

        const cancelationNew = EventService.listen(EventType.new_vehicle, () => onListVehicle());
        const cancelationUpdateDevice = EventService.listen(EventType.update_device, () => onListVehicle());

        return () => {
            FillQuestionHub.unRegister(key);
            cancelationNew();
            cancelationUpdateDevice();
        }
    }, []);

    function handlerNewFillQuestion(message: FillQuestionMessage) {
        setData(old => old?.map(e => {
            if (e.id === message.identifierVehicle) {
                return { ...e, ...message, id: e.id, update: true }
            }
            return e
        }))
    }

    async function onListVehicle() {
        setLoading(true)
        try {

            let result = await VehicleApi.listAll();
            setData(result.data)
        }
        finally {
            setLoading(false)
        }
    }

    const renderBodyRow = (props: any) => {
        let style = props.style;

        if (props?.record?.update) {
            style = { ...style, background: "#FAD24A" }
        }

        return <tr {...props} record={props?.record} style={style} />;
    };

    const renderBodyCell = (props: any) => {

        let onClick = () => {
            push(`/check-parts/history/${props.data.id}`);
            setData(old => old?.map(e => {
                if (e.id === props.data.id) {
                    return { ...e, update: undefined }
                }
                return e
            }))
        }

        if (props.tabletOption) {
            onClick = () => push(`check-parts/select-device/${props.data.id}`, { deviceId: props.data?.deviceId })
        }

        return <td {...props} onClick={onClick} />
    }

    return (
        <>
            <div className="actions-box">
                <div></div>

                <Link to="/check-parts/new">
                    <Button type="primary" icon={<PlusOutlined />} size="large">
                        NOVO
                    </Button>
                </Link>
            </div>

            <Table rowKey="id" columns={colunas} dataSource={data} size="middle"
                components={{
                    body: {
                        row: renderBodyRow,
                        cell: renderBodyCell,
                    }
                }}
                loading={loading}
                onRow={(record,) => {
                    return {
                        onDrag: () => { },
                        record: record
                    }
                }}
            />
        </>
    )
}

export default ListCheckParts
