export enum QuestionPartType {
    DodyworkPart,
    ElectricalPart,
    GeneralPart,
    MandatoryEquipmentPart,
    MotorPart,
    PneusPart
}

export enum QuestionType
{
    Check,
    Multiple
}

export enum QuestionAnswerType
{
    Ok,
    Bad
}

export interface DetailsLastFillQuestionResult_FormQuestion {
    id: string;
    title: string;
    iconUrl: string;
    type: QuestionPartType;
    questions: DetailsLastFillQuestionResult_Question[];
}

export interface DetailsLastFillQuestionResult_Question {
    id: string;
    description: string;
    type: QuestionType;
    answers: DetailsLastFillQuestionResult_QuestionAnswers[];
}

export interface DetailsLastFillQuestionResult_QuestionAnswers {
    key: string;
    description: string;
    type: QuestionAnswerType;
    isSelected: boolean;
}

export interface DetailsLastFillQuestionResult {
    id: string;
    identifierVehicle: string;
    startKm: number;
    endkm: number | null;
    startTime: string;
    endTime: string | null;
    generalOperation: string;
    comments: string;
    formQuestions: DetailsLastFillQuestionResult_FormQuestion[];
}

