import React, { } from 'react'
import { Badge, Button, Col, Row } from 'antd'
import { Input, TextArea } from '../../../../../../components/form/fields/WithFormItem'
import { ToolOutlined } from '@ant-design/icons';
import FormItem from '../../../../../../components/form/form-item/FormItem'
import FormatDateServices from '../../../../../../services/Utils/FormatDate/FormatDateServices';
import SituationVehicle from '../../../../components/situation-vehicle/SituationVehicle';
import { useWatch } from 'react-hook-form';
import { VehiclesSituationType } from '../../../../../../hub/FillQuestionHub/models/FillQuestionMessage';


const FillFormGeneral: React.FC = () => {

    const situation = useWatch<VehiclesSituationType>({ name: "situation" })

    return (
        <>
            <Row>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Motorista" name="driverName" readOnly />
                </Col>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Comandante Equipe" name="commanderName" readOnly />
                </Col>
                <Col sm={24} md={8} lg={6}>
                    <FormItem label="Situação do veículo">
                        <SituationVehicle situation={situation ?? VehiclesSituationType.NEW} />
                    </FormItem>
                </Col>
            </Row>

            <Row>
                <Col sm={24} md={12} lg={6}>
                    <Input label="KM Inicial" name="startKm" readOnly />
                </Col>
                <Col sm={24} md={12} lg={6}>
                    <Input label="KM final" name="endkm" readOnly />
                </Col>
                <Col sm={12} md={8} lg={6}>
                    <Input label="Próxima troca de óleo" name="a" readOnly />
                </Col>
                <Col sm={12} md={8} lg={6}>
                    <FormItem >
                        <Button
                            type="dashed"
                            icon={<ToolOutlined />}
                        >
                            Agendar troca de óleo
                    </Button>
                    </FormItem>
                </Col>
            </Row>

            <Row>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Horário de inicio" name="startTime" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                </Col>
                <Col sm={24} md={8} lg={6}>
                    <Input label="Horário de fim" name="endTime" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                </Col>
            </Row>

            <Row>
                <Col sm={24} md={24} lg={12}>
                    <TextArea label="Funcionamento Geral" rows={4} name="generalOperation" readOnly />
                </Col>
                <Col sm={24} md={24} lg={12}>
                    <TextArea label="Observações" rows={4} name="comments" readOnly />
                </Col>
            </Row>

        </>
    )
}

export default FillFormGeneral
