import SynchronizeService from "./Synchronize/Synchronizes.Service"
import BootstrapService from './Bootstrap/Bootstrap.service';


//Inicializa todos as services 

function init() {
    SynchronizeService.init();
    BootstrapService.init()
}

function dispose() {
    SynchronizeService.dispose();
    BootstrapService.dispose();
}

export { init, dispose }