import React, { useEffect, useState } from 'react'
import { Button, notification } from 'antd';
import { DetailsLastFillQuestionResult } from '../../../../models/queries/DetailsLastFillQuestion';
import { FillQuestionHub } from '../../../../../../hub/FillQuestionHub/FillQuestionHub';
import { HttpStatusCode } from '../../../../../../models/RequestResponse';
import { LoogbookApi } from '../../../../api/LoogbookApi';
import { message } from 'antd';
import { useForm } from 'react-hook-form';
import { useParams, useHistory } from 'react-router-dom';
import Form from '../../../../../../components/form/form/Form';
import FillFormCheckPartResume from '../FillForm/FillFormCheckPartResume';
import FillFormGeneral from '../FillForm/FillFormGeneral';

const LastFillFormContainer: React.FC = () => {
    const { vehicleId } = useParams<{ vehicleId: string }>()
    const { goBack } = useHistory();
    const [loading, setLoading] = useState(false);
    const form = useForm<DetailsLastFillQuestionResult>();

    useEffect(() => {

        getLastFill(vehicleId);

        const key = FillQuestionHub.onReceiveMessage((message1) => {
            openNotification();
        });

        return () => {
            FillQuestionHub.unRegister(key);
        }
    }, [])

    function openNotification() {
        const key = `open${Date.now()}`;

        notification.info({
            key,
            duration: 20,
            message: 'Uma nova ficha recebida.',
            description: renderBodyNotification(key),
        });
    }

    function getLastFill(id: string) {
        setLoading(true);

        LoogbookApi.lastFill(id)
            .then(result => {
                if (result.status === HttpStatusCode.NoContent) {
                    message.info("Veículo não possui ficha preenchida")
                    goBack();
                }
                else {

                }
                form.reset(result.data)
            })
            .finally(() => {
                setLoading(false);
            })
    }

    function renderBodyNotification(key: string) {
        return (
            <div>
                <div>
                    Este veículo acabou de receber uma ficha, deseja atualizar os dados do formúlario?
                </div>

                <div style={{
                    display: "flex",
                    justifyContent: "space-between",
                    paddingTop: 12,
                }}>
                    <Button type="primary" onClick={() => {
                        getLastFill(vehicleId);
                        notification.close(key)
                    }}>Atualizar</Button>
                    <Button onClick={() => notification.close(key)}>Cancelar</Button>
                </div>
            </div>
        )
    }

    return (
        <Form provider={form} loading={loading}>

            <FillFormGeneral />
            <FillFormCheckPartResume />

        </Form>
    )
}

export default LastFillFormContainer
