import React, { useState } from 'react'
import { Layout, Menu } from 'antd';
import { Link } from 'react-router-dom';
import {
    EnvironmentOutlined,
    GlobalOutlined,
    CarOutlined,
    UserOutlined,
    ReconciliationOutlined
} from '@ant-design/icons';

import policiaMilitarBrasao from "./../../images/policia-militar-brasao.png"
import BasicHeader from '../basic-header/BasicHeader';
import { CommonReducerState } from '../../stores/global-reducer';
import { SystemOperationModeType } from '../../stores/System/System.types';
import { useSelector } from 'react-redux';
import "./basic-layout.less"

const { Header, Sider, Content } = Layout;

const BasicLayout: React.FC = (props) => {

    const operationMode = useSelector<CommonReducerState, SystemOperationModeType>(state => state.system.operationMode);

    console.log(operationMode)
    const [collapsed, setCollapsed] = useState(true);

    const toggle = () => {
        setCollapsed(old => !old);
    };

    return (
        <Layout >
            <Sider
                collapsible
                trigger={null}
                collapsed={collapsed}
            >
                <Menu theme="dark" mode="inline" defaultSelectedKeys={['6']}>
                    <Menu.Item key="0" className="logo-brasao" disabled icon={<img src={policiaMilitarBrasao} className="side-menu-icon" alt="" />}>
                        <Link to="/map-2d">
                            Policia Militar
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="2" icon={<EnvironmentOutlined />}>
                        <Link to="/map-2d">
                            Mapa 2d
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="3" icon={<CarOutlined />}>
                        <Link to="/check-parts">
                            Frota
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="4" icon={<UserOutlined />}>
                        <Link to="/manage-users">
                            Usuários
                        </Link>
                    </Menu.Item>
                    <Menu.Item key="5" icon={<ReconciliationOutlined />}>
                        <Link to="/accepted-reports">
                            Copom
                        </Link>
                    </Menu.Item>
                </Menu>
            </Sider>
            <Layout className="site-layout" >
                <Header className="site-layout-background-header" style={{ background: operationMode === SystemOperationModeType.IN_SOS ? "#ff4444" : undefined }}>
                    <BasicHeader collapsed={collapsed} toggle={toggle} />
                </Header>

                <Content className="container" >
                    <div className="site-layout-background" >
                        {props.children}
                    </div>
                </Content>
            </Layout>
        </Layout>
    );
}

export default BasicLayout
