import { Badge } from 'antd'
import React from 'react'
import { AcceptedReportsSituationType } from '../../../../models/queries/ListAcceptedReportsResult'
import { SituationReportProps } from './SituationReport.types'

const SituationReport: React.FC<SituationReportProps> = ({ situation }) => {

    if (situation === AcceptedReportsSituationType.INITIATED) {
        return (<Badge status="processing" text="Iniciado" />)
    }

    if (situation === AcceptedReportsSituationType.FINISHED) {
        return (<Badge status="success" text="Finalizado" />)
    }

    if (situation === AcceptedReportsSituationType.PENDENCY) {
        return (<Badge status="warning" text="Pendência" />)
    }
   
    if (situation === AcceptedReportsSituationType.CANCELED) {
        return (<Badge status="error" text="Cancelado" />)
    }

    return (<Badge status="error" text="Pendência" />)
}

export default SituationReport
