import React, { forwardRef, useCallback, useEffect, useImperativeHandle, useState } from 'react'
import { Col, Row } from 'antd'
import { CreatePoliceComplaintCommand} from '../../models/commands/CreatePoliceComplaintCommand'
import { Input, Switch, Select, TextArea, DatePicker } from '../../../../components/form/fields/WithFormItem'
import { message } from 'antd';
import { PoliceComplaintHandles, PoliceComplaintProps } from './PoliceComplaint.types'
import { PositionReportApi } from '../../api/PositionReportApi'
import { useForm } from 'react-hook-form'
import CreatePoliceComplaintSchema from './CreatePoliceComplaintSchema'
import Form from '../../../../components/form/form/Form'

import "./PoliceComplaint.less"
import { GetReverseGeocodeQuery } from '../../models/queries/GetReverseGeocode';
import { GeoMapApi } from '../../api/GeoMapApi';

const PoliceComplaint = forwardRef<PoliceComplaintHandles, PoliceComplaintProps>((props, forwardedRef) => {

    const defaultValue: CreatePoliceComplaintCommand = {
        latitude: 1,
        longitude: 1,
        isAnonymus: false,
        description: null,
        locationType: null,
        complement: null,
        referencePoint: null,
        locationFeatures: null,
        street: null,
        city: null,
        district: null,
        zipCode: null,
        number: null,
    };

    const [loading,setLoading] = useState<boolean>(false);
    const form = useForm<CreatePoliceComplaintCommand>({ resolver: CreatePoliceComplaintSchema, defaultValues: defaultValue, });

    useEffect(() => {
        var query : GetReverseGeocodeQuery = {
            latitude: props.latitude, 
            longitude: props.longitude
        };
        setLoading(true);
        try {

            GeoMapApi.ReverseGeocode(query).then((item) => {
                form.reset({
                    ...defaultValue,
                    latitude: props.latitude,
                    longitude: props.longitude,
                    street: item.data!.street,
                    city: item.data!.city,
                    district: item.data!.district,
                    zipCode: item.data!.zipCode,
                    number: item.data!.number,
                });
            });
        }
        finally {
            setLoading(false);
        }
    }, [props.latitude, props.longitude]);
    
    useImperativeHandle(forwardedRef, () => ({
        handleSubmit: form.handleSubmit(onSubmit),
        getValues: form.getValues,
        setValues: form.reset
    }))

    const onSubmit = async (data: CreatePoliceComplaintCommand) => {
        setLoading(true);

        try {
            await PositionReportApi.SaveComplaint(data);
            message.success("Ocorrência cadastrada com sucesso!");
            props.onClose();
        }
        catch (e) {
            message.error("Erro crítico.")
        }
        finally {
            setLoading(false)
        }
    };


    return (
        <Form loading={loading} provider={form}  >
            <Input label="latitude" name="latitude" hidden />
            <Input label="longitude" name="longitude" hidden />
            <Row>
                <Col span={24}>
                    <TextArea label="Ocorrência" name="description" rows={4} required />
                </Col>
            </Row>

            <Row>
                <Col span={4}>
                    <Switch label="Anônimo?" name="isAnonymus" checkedChildren={"Sim"} unCheckedChildren={"Não"} />
                </Col>
            </Row>

            <Row>
                <Col span={24}>
                    <Input label="Denunciante" name="whistleblower" required={!form.getValues().isAnonymus} />
                </Col>
            </Row>

            <Row>
                <Col span={16}>
                    <Input label="Cidade" name="city" value="city" />
                </Col>
                <Col span={8}>
                    <Input label="Bairro" name="district" value="district" />
                </Col>
                
            </Row>

            <Row>
                <Col span={12}>
                    <Input label="Rua" name="street" value="street" />
                </Col>
                <Col span={6}>
                    <Input label="Número" name="number" value="number" />
                </Col>
                <Col span={6}>
                    <Input label="CEP" name="zipCode" value="zipCode" />
                </Col>
                
            </Row>

            <Row>
                <Col span={12}>
                    <Input label="Complemento" name="complement" />
                </Col>

                <Col span={12}>
                    <Input label="Tipo de localidade" name="locationType" />
                </Col>
            </Row>

            <Row>
                <Col span={12}>
                    <TextArea label="Ponto de referência" name="referencePoint" />
                </Col>

                <Col span={12}>
                    <TextArea label="Características do local" name="locationFeatures" />
                </Col>
            </Row>

        </Form>
    )
})

export default PoliceComplaint
