import React from 'react'
import FormCreateUser from './pages/form-user/FormUser'
import { Route } from 'react-router-dom';
import ListUsers from './pages/list-registered-users/ListUsers';

const Routes: React.FC = () => {

    return (
        <>
            <Route path="/manage-users" component={ListUsers} />
            <Route path="/manage-users/new" component={FormCreateUser} />
            <Route path="/manage-users/edit/:userId" component={FormCreateUser} />
        </>
    )
}

export default Routes