import * as Yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { CreatePoliceDelationCommand } from "../../models/commands/CreatePoliceDelationCommand";

const CreatePoliceDelationSchema: Yup.SchemaOf<CreatePoliceDelationCommand> = Yup.object().shape({

    latitude: Yup.number().nullable().required(),

    longitude: Yup.number().nullable().required(),

    description: Yup.string().nullable()
        .required("Ocorrência é obrigatória.")
        .min(3, "Ocorrência é obrigatória.")
        .max(280, "Ocorrência deve conter menos de 280 letras!"),

    isAnonymus: Yup.boolean().optional(),

    whistleblower: Yup.string().when("isAnonymus", {
        is: false,
        then: Yup.string().nullable()
            .required("Denunciante é obrigatório.")
            .min(3, "Denunciante é obrigatório.")
            .max(30, "Denunciante deve conter menos de 30 letras!"),
        otherwise: Yup.string().nullable().optional(),
    }),

    daysOfWeek: Yup.mixed().nullable()
        .required("Informe os dias."),

    dateTime: Yup.date().nullable()
        .required("Informe a data da ocorrência."),

    locationType: Yup.string().nullable()
        .optional()
        .max(100, "Tipo de localidade deve conter menos de 20 letras!"),

    complement: Yup.string().nullable()
        .optional()
        .max(100, "Complemento deve conter menos de 20 letras!"),

    referencePoint: Yup.string().nullable()
        .optional()
        .max(100, "Ponto de referencia deve conter menos de 50 letras!"),

    locationFeatures: Yup.string().nullable()
        .optional()
        .max(100, "Caracteristicas do local deve conter menos de 100 letras!"),

    street: Yup.string().nullable()
        .optional()
        .max(100, "Rua deve conter menos de 20 letras!"),

    city: Yup.string().nullable()
        .optional()
        .max(100, "Cidade deve conter menos de 20 letras!"),

    district: Yup.string().nullable()
        .optional()
        .max(100, "Bairro deve conter menos de 20 letras!"),

    zipCode: Yup.string().nullable()
        .optional()
        .max(9, "CEP: xxxxx-xxx"),

    number: Yup.string().nullable()
        .optional()
})



export default yupResolver<CreatePoliceDelationCommand>(CreatePoliceDelationSchema);