import React, { forwardRef, useEffect, useImperativeHandle, useState } from 'react'
import { Col, Row, Select as SelectAntd } from 'antd'
import { CreatePoliceDelationCommand } from '../../models/commands/CreatePoliceDelationCommand';
import { DatePicker, Input, Select, Switch, TextArea } from '../../../../components/form/fields/WithFormItem'
import { message } from 'antd';
import { PoliceDelationHandles, PoliceDelationProps } from './PoliceDelation.types'
import { PositionReportApi } from '../../api/PositionReportApi'
import { useForm } from 'react-hook-form'
import CreatePoliceDelationSchema from './CreatePoliceDelationSchema'
import Form from '../../../../components/form/form/Form'

import "./PoliceDelation.less"
import { DayOfWeek } from '../../models/common/CommonPoliceReport';
import { GetReverseGeocodeQuery } from '../../models/queries/GetReverseGeocode';
import { GeoMapApi } from '../../api/GeoMapApi';

const PoliceDelation = forwardRef<PoliceDelationHandles, PoliceDelationProps>((props, forwardedRef) => {

    const { Option } = SelectAntd;
    const { Sunday, Monday, Tuesday, Wednesday, Thursday, Friday, Saturday } = DayOfWeek;

    const defaultValue: CreatePoliceDelationCommand = {
        latitude: 1,
        longitude: 1,
        isAnonymus: false,
        dateTime: null,
        daysOfWeek: [],
        description: null,
        locationType: null,
        complement: null,
        referencePoint: null,
        locationFeatures: null,
        street: null,
        city: null,
        district: null,
        zipCode: null,
        number: null,
    };

    const [loading, setLoading] = useState<boolean>(false);
    const form = useForm<CreatePoliceDelationCommand>({ resolver: CreatePoliceDelationSchema, defaultValues: defaultValue, });
    
    useEffect(() => {
        var query : GetReverseGeocodeQuery = {
            latitude: props.latitude, 
            longitude: props.longitude
        };
        setLoading(true);
        try {
            GeoMapApi.ReverseGeocode(query).then((item) => {

                form.reset({
                    ...defaultValue,
                    latitude: props.latitude,
                    longitude: props.longitude,
                    street: item.data!.street,
                    city: item.data!.city,
                    district: item.data!.district,
                    zipCode: item.data!.zipCode,
                    number: item.data!.number,
                });
            });
        }
        finally {
            setLoading(false);
        }
    }, [props.latitude, props.longitude]);

    useImperativeHandle(forwardedRef, () => ({
        handleSubmit: form.handleSubmit(onSubmit),
        getValues: form.getValues,
        setValues: form.reset
    }))


    const onSubmit = async (data: CreatePoliceDelationCommand) => {
        setLoading(true);

        try {
            await PositionReportApi.SaveDelation(data);
            message.success("Denúncia cadastrada com sucesso!");
            props.onClose();
        }
        catch (e) {
            message.error("Erro crítico.")
        }
        finally {
            setLoading(false)
        }
    };

    return (
        <Form loading={loading} provider={form}  >
            <Input label="latitude" name="latitude" hidden />
            <Input label="longitude" name="longitude" hidden />
            <Row>
                <Col span={24}>
                    <TextArea label="Denúncia" name="description" rows={4} required />
                </Col>
            </Row>

            <Row>
                <Col span={4}>
                    <Switch label="Anônimo?" name="isAnonymus" checkedChildren={"Sim"} unCheckedChildren={"Não"} />
                </Col>
            </Row>

            <Row>
                <Col span={24}>
                    <Input label="Denunciante" name="whistleblower" required={!form.getValues().isAnonymus} />
                </Col>
            </Row>

            <Row>
                <Col span={16}>
                    <Input label="Cidade" name="city" value="city" />
                </Col>
                <Col span={8}>
                    <Input label="Bairro" name="district" value="district" />
                </Col>
                
            </Row>

            <Row>
                <Col span={12}>
                    <Input label="Rua" name="street" value="street" />
                </Col>
                <Col span={6}>
                    <Input label="Número" name="number" value="number" />
                </Col>
                <Col span={6}>
                    <Input label="CEP" name="zipCode" value="zipCode" />
                </Col>
                
            </Row>

            <Row>
                <Col span={8}>
                    <DatePicker
                        required
                        label="Data da ocorrência"
                        name="dateTime"
                        format="DD/MM/yyyy HH:mm:ss"
                        showTime
                    />
                </Col>

                <Col span={16} >
                    <Select label="Dias da semana" name="daysOfWeek" mode="multiple"  >
                        <Option value={Sunday}>Domingo</Option>
                        <Option value={Monday}>Segunda</Option>
                        <Option value={Tuesday}>Terça</Option>
                        <Option value={Wednesday}>Quarta</Option>
                        <Option value={Thursday}>Quinta</Option>
                        <Option value={Friday}>Sexta</Option>
                        <Option value={Saturday}>Sabado</Option>
                    </Select>
                </Col>
            </Row>

            <Row>
                <Col span={12}>
                    <Input label="Complemento" name="complement" />
                </Col>

                <Col span={12}>
                    <Input label="Tipo de localidade" name="locationType" />
                </Col>
            </Row>

            <Row>
                <Col span={12}>
                    <TextArea label="Ponto de referência" name="referencePoint" />
                </Col>

                <Col span={12}>
                    <TextArea label="Características do local" name="locationFeatures" />
                </Col>
            </Row>

        </Form>
    )
})

export default PoliceDelation
