import { HubBase } from "../Hub";
import { ReceiveCancelSOSVehicleMessage } from "./models/ReceiveCancelSOSVehicleMessage";
import { ReceiveLocationVehicleMessage } from "./models/ReceiveLocationVehicleMessage";
import { ReceiveNeedSOSVehicleMessage } from "./models/ReceiveNeedSOSVehicleMessage";
import { ReceivePoliceComplaintReportMessage } from "./models/ReceivePoliceComplaintReportMessage";
import { ReceivePoliceDelationReportMessage } from "./models/ReceivePoliceDelationReportMessage";
import { ReceiveUpdateAcceptPoliceReportMessage } from "./models/ReceiveUpdateAcceptPoliceReportMessage";

export const endPoint: string = 'hubs/notification';

class Hub extends HubBase {

    constructor() {
        super(endPoint);
    }

    onReceiveLocation(newMethod: (args: ReceiveLocationVehicleMessage) => void): string {
        return super.register("onReceiveLocation", newMethod)
    }

    onReceiveCancelSOS(newMethod: (args: ReceiveCancelSOSVehicleMessage) => void): string {
        return super.register("onReceiveCancelSOS", newMethod)
    }

    onReceiveNeedSOS(newMethod: (args: ReceiveNeedSOSVehicleMessage) => void): string {
        return super.register("onReceiveNeedSOS", newMethod)
    }

    onReceivePoliceComplaintReport(newMethod: (args: ReceivePoliceComplaintReportMessage) => void): string {
        return super.register("onReceivePoliceComplaintReport", newMethod)
    }
    
    onReceivePoliceDelationReport(newMethod: (args: ReceivePoliceDelationReportMessage) => void): string {
        return super.register("onReceivePoliceDelationReport", newMethod)
    }

    onReceiveUpdateAcceptPoliceReport(newMethod: (args: ReceiveUpdateAcceptPoliceReportMessage) => void): string {
        return super.register("onReceiveUpdateAcceptPoliceReport", newMethod)
    }
}

export const NotificationHub = new Hub();