import { HubConnection, HubConnectionBuilder } from '@microsoft/signalr';
import { message } from 'antd';
import { MessageType } from 'antd/lib/message';

export class HubBase {

    private EndPoint: string;
    private Connection?: HubConnection;
    private RegistredTopic: any;
    private TriggerTopic: any;

    private messagesClose: MessageType[] = [];

    private LastUid = 0;
    private Timeout?: NodeJS.Timeout;

    constructor(endPoint: string) {

        this.RegistredTopic = {};
        this.TriggerTopic = {};
        this.EndPoint = endPoint;

    }

    private async _configureConnection() {
        if (this.Connection) {

            return;
        }

        let baseUri = "https://genesis-pm.herokuapp.com";

        if (process.env.NODE_ENV === "development") {
            baseUri = "http://localhost:5000"
        }

        this.Connection = new HubConnectionBuilder()
            .withUrl(`${baseUri}/${this.EndPoint.replace("//", "/")}`)
            .withAutomaticReconnect()
            .build();

        await this.Connection.start();

        this.Connection.onclose(e => {
            console.log("Caiu", e)

            let messagemClosabe = message.error('Conexão em tempo real perdida!', 100000000);

            this.messagesClose = [...this.messagesClose, messagemClosabe];
        })

        this.Connection.onreconnecting(e => {
            console.log("Caiu", e)

            let messagemClosabe = message.error('Conexão em tempo real está instável!', 100000000);
            this.messagesClose = [...this.messagesClose, messagemClosabe];
        })

        this.Connection.onreconnected(e => {
            this.closaAllMessages();

            message.success("Conexão em tempo real restabelecida!")
        })
    }

    private _deliverMessage(topic: string, message: any[]) {

        console.log("Chegou mensagem")

        if (!this.RegistredTopic.hasOwnProperty(topic)) {
            return;
        }

        let subscribers: any[] = this.RegistredTopic[topic];

        Object.keys(subscribers).forEach(sub => {
            this.RegistredTopic[topic][sub](message);
        })
    }

    async invoke(topic: string, message: any) {
        await this._configureConnection();

        await this.Connection?.invoke(topic, message);
    }

    register(topic: string, func: (...args: any[]) => void): string {

        if (typeof func !== 'function') {
            throw new Error("Função inválida");;
        }


        console.info("Registrando função");
        // message is not registered yet
        if (!this.RegistredTopic.hasOwnProperty(topic)) {
            this.RegistredTopic[topic] = {};

            this._configureConnection()
                .then(result => {
                    console.info(`Connected! => ${this.EndPoint} `);

                    this.Connection?.on(topic, message => {
                        this._deliverMessage(topic, message);
                    });
                })
                .catch(e => {
                    console.info(`Connection failed: => ${this.EndPoint}`, e);

                    setTimeout(() => {
                        console.info(`Try reconnect: => ${this.EndPoint}`, e);
                        this.register(topic, func);
                    }, 5000);

                });
        }

        // forcing token as String, to allow for future expansions without breaking usage
        // and allow for easy use as key names for the 'messages' object
        var token = 'uid_' + String(++this.LastUid);
        this.RegistredTopic[topic][token] = func;

        // return token for unsubscribing
        return token;
    }

    unRegister(token: string) {

        Object.keys(this.RegistredTopic).forEach(value => {
            delete this.RegistredTopic[value][token];

            clearInterval(this.Timeout!);
            this.Timeout = setTimeout(() => {
                if (!this.RegistredTopic) {
                    return;
                }

                if (Object.keys(this.RegistredTopic[value])?.length === 0) {
                    console.log("Desregistrando")
                    this.Connection?.off(value);
                    delete this.RegistredTopic[value];
                }

                if (Object.keys(this.RegistredTopic)?.length === 0 && Object.keys(this.TriggerTopic)?.length === 0) {
                    console.log("Destruindo")
                    this.Connection?.stop();
                    this.Connection = undefined;
                }
            }, 10000);

        })
    }

    private closaAllMessages() {
        this.messagesClose.forEach(messageClose => messageClose())

        this.messagesClose = [];
    }

}