import React, { useEffect, useMemo, useState } from 'react'
import { AcceptedReportApi } from '../../api/AcceptedReportApi';
import { Col, Divider, message, Modal, Row, Table } from 'antd'
import { ColumnsType } from 'antd/lib/table';
import { Controller, useForm } from 'react-hook-form';
import { DetailPoliceReportResult, DetailPoliceReportResult_AcceptHistoric } from '../../models/queries/DetailsAcceptedReports';
import { HttpStatusCode } from '../../../../models/RequestResponse';
import { Input, TextArea } from '../../../../components/form/fields/WithFormItem';
import { useHistory, useParams } from 'react-router-dom';
import Form from '../../../../components/form/form/Form';
import FormatDateServices from '../../../../services/Utils/FormatDate/FormatDateServices';
import "./ViewAcceptedReport.less";
import { PositionReportType } from '../../models/common/CommonPoliceReport';

const ViewAcceptedReport: React.FC = () => {

    const { positionReportId } = useParams<{ positionReportId: string }>()
    const { goBack } = useHistory()
    const [loading, setLoading] = useState(false);
    const [reportType, setReportType] = useState<PositionReportType>(PositionReportType.COMPLAINT)
    const form = useForm<DetailPoliceReportResult>();

    const columnsHistoric: ColumnsType<DetailPoliceReportResult_AcceptHistoric> = useMemo(() => [
        {
            key: "prefix",
            dataIndex: "prefix",
            title: "Prefixo",
        },
        {
            key: "acceptedAt",
            dataIndex: "acceptedAt",
            title: "Aceito em",
            render: (date: Date) => FormatDateServices.formatDateTime(date)
        },
        {
            key: "finishedAt",
            dataIndex: "finishedAt",
            title: "Finalizado em",
            render: (date?: Date) => date ? FormatDateServices.formatDateTime(date) : "-"
        },
        {
            key: "description",
            dataIndex: "description",
            title: "Descrição",
            render: (text?: string) => text ?? "-"
        },
        {
            key: "canceledAt",
            dataIndex: "canceledAt",
            title: "Cancelado em",
            render: (date?: Date) => date ? FormatDateServices.formatDateTime(date) : "-"
        },
    ] as ColumnsType<DetailPoliceReportResult_AcceptHistoric>, [])

    useEffect(() => {

        getReportItem(positionReportId);

    }, []);



    function getReportItem(id: string) {
        setLoading(true);

        AcceptedReportApi.getItem(id)
            .then(result => {
                if (result.status === HttpStatusCode.NotFound) {
                    message.info("Ocorrência inválida")
                    goBack();
                }
                form.reset(result.data);
                setReportType(result.data?.type!)
            })
            .finally(() => {
                setLoading(false);
            }
            );
    }

    return (
        <Modal
            visible
            bodyStyle={{ paddingTop: 0 }}
            cancelText="VOLTAR"
            width="900px"
            title={`Visualizar ${reportType === PositionReportType.COMPLAINT ? "ocorrência" : "denúncia"}`}
            onCancel={() => { goBack() }}
            onOk={() => { goBack() }}
        >

            <Form provider={form} loading={loading} >
                <div className="view-accepted-reports-body">
                    <Row>
                        <Col span={6}>
                            <Input label="Código da Ocorrência" name="code" readOnly />
                        </Col>
                        <Col span={6}>
                            <Input label="Prefixo do veículo" name="prefix" readOnly />
                        </Col>
                        <Col span={12}>
                            <Input label="Denunciante" name="whistleblower" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={8}>
                            <Input label="Ponto de referência" name="code" readOnly />
                        </Col>
                        <Col span={6}>
                            <Input label="Prefixo do veículo" name="prefix" readOnly />
                        </Col>
                        <Col span={8}>
                            <Input label="Denunciante" name="whistleblower" readOnly />
                        </Col>
                    </Row>

                    <Row>
                        <Col span={24}>
                            <TextArea label="Detalhes" rows={6} name="description" readOnly />
                        </Col>
                    </Row>


                    <Row>
                        <Col span={6}>
                            <Input label="Criado em" name="createdAt" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>
                        <Col span={6}>
                            <Input label="Aceitado em" name="acceptedAt" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>
                        <Col span={6}>
                            <Input label="Finalizado em" name="finishedAt" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>
                        <Col span={6}>
                            <Input label="Cancelado em" name="canceledAt" readOnly render={(value: Date) => FormatDateServices.formatDateTime(value)} />
                        </Col>
                    </Row>
                </div>

                <Controller
                    name="acceptHistoric"
                    render={({ value: historic }) => {

                        return (
                            <>
                                <Divider orientation="left" plain>
                                    Histórico
                                 </Divider>

                                <Table size="middle" columns={columnsHistoric} dataSource={historic} />
                            </>
                        )
                    }}
                />

            </Form>
        </Modal>
    )
}

export default ViewAcceptedReport;
