import api from '../../../api/Api.configure';
import { ApiBase } from "../../../api/Api";
import { RequestResponse } from '../../../models/RequestResponse';
import { ListAcceptedReportsResult } from '../models/queries/ListAcceptedReportsResult';
import { DetailPoliceReportResult } from '../models/queries/DetailsAcceptedReports';
import { UpdatePoliceComplaintCommand } from '../models/commands/UpdatePoliceComplaintCommand';
import { UpdatePoliceDelationCommand } from '../models/commands/UpdatePoliceDelationCommand';
import { GetPoliceComplaintResult } from '../models/queries/GetPoliceComplaintResult';
import { GetPoliceDelationResult } from '../models/queries/GetPoliceDelationResult';

export const endPoint: string = 'api/position-report';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async cancelReport(identifierReport: string): Promise<RequestResponse<void>> {
        return await api.post(`${endPoint}/cancel/${identifierReport}`)
    }

    async listAll(): Promise<RequestResponse<ListAcceptedReportsResult[]>> {
        return (await api.get(`${endPoint}/list-all`)).data;
    }

    async getItem(IdentifierReport: string): Promise<RequestResponse<DetailPoliceReportResult>> {
        return await (await api.get(`${endPoint}/${IdentifierReport}`)).data;
    }
    
    async getReportComplaint(IdentifierReport: string): Promise<RequestResponse<GetPoliceComplaintResult>> {
        return await (await api.get(`${endPoint}/get-police-complaint/${IdentifierReport}`)).data;
    }

    async getReportDelation(IdentifierReport: string): Promise<RequestResponse<GetPoliceDelationResult>> {
        return await (await api.get(`${endPoint}/get-police-delation/${IdentifierReport}`)).data;
    }

    async updatePoliceComplaint(command: UpdatePoliceComplaintCommand): Promise<RequestResponse<UpdatePoliceComplaintCommand>> {
        return (await api.put(`${endPoint}/update-police-complaint`, command)).data
    }
    
    async updatePoliceDelation(command: UpdatePoliceDelationCommand): Promise<RequestResponse<UpdatePoliceDelationCommand>> {
        return (await api.put(`${endPoint}/update-police-delation`, command)).data
    }
}

export const AcceptedReportApi = new Api();