import api from '../../../api/Api.configure';
import { ApiBase } from "../../../api/Api";
import { RequestResponse } from "../../../models/RequestResponse";
import { DetailUserResult, ListUsersResult } from "../models/queries/ListUsersResult";
import { CreateUserCommand, CreateUserResult, UpdateUserCommand } from "../models/commands/CreateUser";

export const endPoint: string = 'api/users';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async getUser(userId: string): Promise<RequestResponse<DetailUserResult>> {
        return (await api.get(`${endPoint}/${userId}`)).data
    }

    async listAll(): Promise<RequestResponse<ListUsersResult[]>> {
        return (await api.get(`${endPoint}/list-all`)).data
    }

    async create(command: CreateUserCommand): Promise<RequestResponse<CreateUserResult>> {
        return (await api.post(`${endPoint}`, command)).data
    }

    async update(command: UpdateUserCommand): Promise<RequestResponse<CreateUserResult>> {
        return (await api.put(`${endPoint}/${command.id}`, command)).data
    }

}

export const UserApi = new Api();