import React from 'react'
import { Controller } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import { WithFormITemProps } from './WithFormItem.types';
import * as Antd from "antd"
import FormItem from '../form-item/FormItem';
import FormSwitch from './Switch/FormSwitch';
import locale from 'antd/es/date-picker/locale/pt_BR';
export const WithFormItem = <P extends object>(Field: React.ComponentType<P>, propsConf?: any): React.FC<P & WithFormITemProps> => (props: any) => {

    return (
        <Controller
            name={props.name}
            defaultValue={props.value ?? null}
            key={props.name}
            render={({ onChange, onBlur, value }) =>
                <>
                    {props.hidden !== true &&
                        <FormItem label={props.label || ""} required={props.required}>
                            <Field autoComplete="off"
                                {...propsConf}
                                {...props}
                                required={false}
                                style={props.style ? props.style : { width: "100%" }}
                                onChange={onChange}
                                onBlur={onBlur}
                                value={props.render ? props.render(value) : value} />

                            <ErrorMessage name={props.name} render={({ message }) => <p style={{ color: "red" }}>{message}</p>} key={props.name} />
                        </FormItem>}
                </>
            } />
    )
}



export const Input = WithFormItem(Antd.Input, {});
export const InputNumber = WithFormItem(Antd.InputNumber, { decimalSeparator: "," });
export const TextArea = WithFormItem(Antd.Input.TextArea, {});

export const Select = WithFormItem(Antd.Select, {})

export const DatePicker = WithFormItem(Antd.DatePicker, { format: "DD/MM/yyyy", locale: locale });
export const RangePicker = WithFormItem(Antd.DatePicker.RangePicker, { fast: true, format: "DD/MM/yyyy", locale: locale })

export const Switch = WithFormItem(FormSwitch, {});

