import React, { useEffect, useMemo, useState } from 'react'
import { ColumnsType } from 'antd/lib/table'
import { ListHistoricVehicleResult, VehiclesSituationType } from '../../../../models/queries/ListHistoricVehicle'
import { LoogbookApi } from '../../../../api/LoogbookApi'
import { Table } from 'antd'
import { useHistory, useParams } from 'react-router-dom'
import FormatDateServices from '../../../../../../services/Utils/FormatDate/FormatDateServices'
import SituationVehicle from '../../../../components/situation-vehicle/SituationVehicle'

const ListHistoricGeneral: React.FC = () => {

    const { vehicleId } = useParams<{ vehicleId: string }>()
    const { push } = useHistory()

    const [loading, setLoading] = useState(false)
    const [historic, setHistoric] = useState<ListHistoricVehicleResult[]>([])

    const columnsHistoric = useMemo(() => [
        {
            key: "startTime",
            dataIndex: "startTime",
            title: "Horário de inicio",
            render: (value?: Date) => value ? FormatDateServices.formatDateTime(value) : "-"
        },
        {
            key: "endTime",
            dataIndex: "endTime",
            title: "Horário de fim",
            render: (value?: Date) => value ? FormatDateServices.formatDateTime(value) : "-"
        },
        {
            key: "startKm",
            dataIndex: "startKm",
            title: "KM inicial"
        },
        {
            key: "endkm",
            dataIndex: "endkm",
            title: "KM Final",
            render: (value?: number) => value ?? "-"
        },
        {
            title: 'Situação veículo',
            key: 'situation',
            dataIndex: "situation",
            width: "130px",
            render: (situation: VehiclesSituationType) => <SituationVehicle situation={situation} />
        },
    ] as ColumnsType<ListHistoricVehicleResult>, [])

    useEffect(() => {
        loadItems(vehicleId);
    }, [])

    async function loadItems(id: string) {
        setLoading(true);
        try {

            let result = await LoogbookApi.listHistoric(id);
            setHistoric(result.data!)
        }
        finally {
            setLoading(false);
        }
    }

    return (
        <div>
            <Table size="middle"
                columns={columnsHistoric}
                dataSource={historic}
                loading={loading}
                onRow={(record) => {
                    return {
                        onClick: () => {
                            push(`/check-parts/history/${vehicleId}/detail/${record.id}`);
                        },
                        record: record
                    }
                }} />

        </div>
    )
}

export default ListHistoricGeneral
