import React from 'react'
import { FormProps } from './Form.types'
import { FormProvider } from 'react-hook-form'
import { Spin } from 'antd'
import AntdForm from 'antd/lib/form/Form'

import "./form-overlayer-loaging-styles.less"

const Form: React.FC<FormProps<any>> = ({ provider, children, loading }) => {

    return (
        <AntdForm layout="vertical">
            <FormProvider {...provider}  >
                    {children}
            </FormProvider>

            {loading && <div className={"form-overlayer-loaging"} >
                <Spin tip="Carregando..." size="large" style={{ position: "absolute" }} />
            </div>}
        </AntdForm >
    )
}

export default Form