import React, { useEffect, useMemo, useState } from 'react'
import { ColumnsType } from 'antd/lib/table';
import { IListApproachResult } from '../../models/queries/ListApproach';
import { LogbookApi } from '../../api/LogbookApi';
import { PageHeader, Table } from 'antd'
import { useHistory } from 'react-router-dom';
import FormatDateServices from '../../../../services/Utils/FormatDate/FormatDateServices';

const ListApproachReports: React.FC = () => {

    const { push } = useHistory();
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState<IListApproachResult[]>();

    const colunas = useMemo(() => [
        {
            title: 'Equipe',
            dataIndex: 'team',
            key: 'team',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Prefixo',
            dataIndex: 'veheclePrefix',
            key: 'veheclePrefix',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Motorista',
            dataIndex: 'driverName',
            key: 'driverName',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Comandante',
            dataIndex: 'commanderName',
            key: 'commanderName',
            render: (value) => value ?? "-",
            onCell: (data) => ({ data }),
        },
        {
            title: 'Descrição',
            dataIndex: 'descriptions',
            key: 'descriptions',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Km total',
            dataIndex: 'totalkm',
            key: 'totalkm',
            onCell: (data) => ({ data }),
        },
        {
            title: 'Turno',
            key: 'turno',
            width: 300,
            render: (value, record) => `${FormatDateServices.formatDateTime(record.startTime)} as ${FormatDateServices.formatDateTime(record.endTime)}`
        },
    ] as ColumnsType<IListApproachResult>, []);

    useEffect(() => {
        onListAllApproach();
    }, []);


    async function onListAllApproach() {
        setLoading(true)
        try {
            let result = await LogbookApi.listAllApproach();
            setData(result.data)
        }
        finally {
            setLoading(false)
        }
    }

    const renderBodyCell = (props: any) => {

        console.log(props)

        let onClick = () => {
            push(`accepted-reports/report-approaches/${props.data.id}`);
        }

        if (props.tabletOption) {
            onClick = () => { }
        }

        return <td {...props} onClick={onClick} />
    }

    const renderBodyRow = (props: any) => {

        if (props?.record?.update) {
            return <tr {...props} style={{ background: "#FAD24A" }} />;
        }

        return <tr {...props} />;
    };

    return (
        <>
            <PageHeader title="Relatórios de abordagens" />

            <Table rowKey="id"
                columns={colunas}
                dataSource={data} size="middle"
                components={{
                    body: {
                        row: renderBodyRow,
                        cell: renderBodyCell,
                    }
                }}
                loading={loading}
                pagination={false}
                onRow={(record) => {
                    return {
                        onDrag: () => { },
                        record: record
                    }
                }}
            />
        </>
    )
}

export default ListApproachReports
