import { ApiBase } from "../Api";
import { GetStateResult } from "./models/GetStateResult";
import { RequestResponse } from "../../models/RequestResponse";
import api from "../Api.configure";

export const endPoint: string = 'api/bootstrap';

class Api extends ApiBase {

    constructor() {
        super(endPoint);
    }

    async GetState(): Promise<RequestResponse<GetStateResult>> {
        return (await api.get(`${endPoint}`)).data
    }
}
export const BootstrapApi = new Api();